export const getObjectFromSVGPath = async (path: string) => {
    let parser = new DOMParser();
    let response = await fetch(path);
    let text = await response.text();
    let xml = await parser.parseFromString(text, 'text/xml');
    let d = xmlToJson(xml);
    return d;
};
export const getObjectFromSVGImporting = async (content: string) => {
    let parser = new DOMParser();
    let xml = await parser.parseFromString(content, 'text/xml');
    let d = xmlToJson(xml);
    return d;
};
export const xmlToJson = (xml: XMLDocument) => {
    let obj = {}; // Create the return object
    if (xml.nodeType === 1) { // element
        // eslint:disable-next-line:no-string-literal
        if (xml['attributes'].length > 0) {// do attributes
            obj['@attributes'] = {};
            // eslint:disable-next-line:no-string-literal
            for (let j = 0; j < xml['attributes'].length; j++) {
                // eslint:disable-next-line:no-string-literal
                let attribute = xml['attributes'].item(j);
                obj['@attributes'][attribute.nodeName] = attribute.nodeValue;
            }
        }
    } else if (xml.nodeType === 3) { // text
        if (xml.nodeValue) { obj = xml.nodeValue; }
    }
    if (xml.hasChildNodes()) { // do children
        for (let i = 0; i < xml.childNodes.length; i++) {
            // eslint:disable-next-line:no-any
            let item = xml.childNodes.item(i) as any;
            let nodeName = item.nodeName;
            if (typeof (obj[nodeName]) === 'undefined') {
                obj[nodeName] = xmlToJson(item);
            } else {
                if (typeof (obj[nodeName].push) === 'undefined') {
                    let old = obj[nodeName];
                    obj[nodeName] = [];
                    obj[nodeName].push(old);
                }
                obj[nodeName].push(xmlToJson(item));
            }
        }
    }
    return obj;
};
