import { FrameShapeType, FrameRatioPreset } from '../../../../lib';
export class ParamCropping {
    public img: HTMLImageElement;
    public x: number = 0;
    public y: number = 0;
    public width: number = 10;
    public height: number = 10;
    public scale: number = 1.0;
    public rotation: number = 0.0;
    public isCropGraphics: boolean = true;
    public shape: FrameShapeType = FrameShapeType.Rectangle;
    public aspectRatio: FrameRatioPreset = FrameRatioPreset.Original;
    public toJSON() {
        return {
            x: this.x,
            y: this.y,
            width: this.width,
            height: this.height,
            scale: this.scale,
            rotation: this.rotation,
            shape: this.shape,
            aspectRatio: this.aspectRatio,
            isCropGraphics: this.isCropGraphics,
        };
    }
    public toString() {
        return JSON.stringify(this.toJSON());
    }
    public update(x: number, y:number, width: number, height: number, scale: number, rotation: number, shapeType: FrameShapeType, aspectRatio: FrameRatioPreset) {
        // debugger
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
        this.scale = scale;
        this.rotation = rotation;
        this.shape = shapeType;
        this.aspectRatio = aspectRatio;
        // console.log(this);
        // console.trace();
    }
}
