/*
This is a Scene Manager for undo and redo operation.
Whenever you do actions like add, remove, or translation,
we capture the geometry data in the history that allow us
to go back and forth.
*/
export class SceneManager {
  protected history: any = [];
  protected currentIndex = -1;
  constructor() {
    /* */
  }
  public reset() {
    this.history = [];
    this.currentIndex = -1;
  }
  public push(data: any) {
    if (this.currentIndex < this.history.length - 1) {
      this.history.splice(this.currentIndex + 1, this.history.length);
    }
    this.history.push(data);
    this.currentIndex = this.history.length - 1;
  }
  public undo() {
    if (this.currentIndex === 0) {
      return undefined;
    }
    this.currentIndex--;
    return this.history[this.currentIndex];
  }
  public redo() {
    this.currentIndex++;
    this.currentIndex =
      this.currentIndex < this.history.length ? this.currentIndex : this.history.length - 1;
    return this.history[this.currentIndex];
  }
  public getIndexCurrent() {
    return this.currentIndex;
  }
  public getLengthHistory() {
    return this.history.length;
  }
  public getCurrentState() {
    return { currentIndex: this.getIndexCurrent(), historyNum: this.getLengthHistory() };
  }
}
