import { ExpressImageAppType, ActionType, delimiter, ActiveCommandMode } from '../../../lib';
import { IO } from '..';
export class StateProcessing {
  public io: IO;
  constructor(io: IO) {
    this.io = io;
  }
  public pushAction(actionIn: string ) {
    const index = actionIn.indexOf(delimiter);
    const action = index < 0 ? [actionIn] : [actionIn.substring(0, index), actionIn.substring(index + 1, actionIn.length)];
    const param = action[1];

    switch (action[0]) {
      case 'style':
        // // eslint-disable-next-line no-case-declarations
        // const obj = JSON.parse(action[1]);
        // this.io.imageAnnoApp.geometryCommon.gcmdProcessing.updateStyleCustom(obj);
        break;
      case 'activateApp':
        this.io.controller.app.api.onClickAppendApp(param);
        break;
      case 'destroyApp':
        this.destroyApp(param as ExpressImageAppType);
        break;
      case ActionType.SetDrawingMode:
        const command = action[0] + '@';
        // console.log(command, param);
        this.setDrawingMode(param as ActiveCommandMode, command);
        break;
      default:
        break;
    }
  }
  private destroyApp(param: ExpressImageAppType) {
    switch (param) {
      case ExpressImageAppType.Drawing:
        this.io.controller.app.api.destroyDrawing();
        break;
      case ExpressImageAppType.Editing:
        this.io.controller.app.api.destroyEditing();
        break;
      default:
        break;
    }
  }
  private setDrawingMode(param: ActiveCommandMode, command: string) {
    switch (param) {
      case ActiveCommandMode.SELECT:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.SELECT);
        break;
      case ActiveCommandMode.POINT_REGULAR:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.POINT_REGULAR);
        break;
      case ActiveCommandMode.POINT_NUMBERED:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.POINT_NUMBERED);
        break;
      case ActiveCommandMode.POLYLINE_BENDING:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.POLYLINE_BENDING);
        break;
      case ActiveCommandMode.FREEHAND_POLYLINE_BENDING:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.FREEHAND_POLYLINE_BENDING);
        break;
      case ActiveCommandMode.POLYGON:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.POLYGON);
        break;
      case ActiveCommandMode.FREEHAND_POLYGON:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.FREEHAND_POLYGON);
        break;
      case ActiveCommandMode.RECTANGLE:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.RECTANGLE);
        break;
      case ActiveCommandMode.CIRCLE:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.CIRCLE);
        break;
      case ActiveCommandMode.ARROW_SINGLE:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.ARROW_SINGLE);
        break;
      case ActiveCommandMode.ARROW_DOUBLE:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.ARROW_DOUBLE);
        break;
      case ActiveCommandMode.TEXT_LEADERLINE:
        this.io.controller.app.api.setDrawingMode(command + ActiveCommandMode.TEXT_LEADERLINE);
        break;
      default:
        break;
    }
  }
}
