import {
    FrameRatioPreset, FrameShapeType, ImageType, cutOutContextByShape, contextMergeImgDrawing,
    getRegularPointAGSM, getRegularPointSMX, getNumberedPointSMX,
    Environment, ExpressImageAppType
} from '../../../lib';
import { ParamCropping, ParamDrawing, ParamEffecting } from './DataModelBase';
import { ImageWrapper } from '..';
import { getUUID } from '../../../../../lib/Utility/MathUtility';

export class ExpressImageData {
    public imageWrapper: ImageWrapper;
    public paramCropping: ParamCropping;
    public paramEffecting: ParamEffecting;
    public paramDrawing: ParamDrawing;
    public typeImageCompression: ImageType = ImageType.Png;
    private ctx: CanvasRenderingContext2D;
    private ctxFiltered: CanvasRenderingContext2D;
    private init = true;
    constructor(imageWrapper: ImageWrapper) {
        this.imageWrapper = imageWrapper;
        this.typeImageCompression = this.imageWrapper.implementation.app.typeImageCompression;
        this.initData();
    }
    public initData() {
        this.paramCropping = new ParamCropping();
        this.paramEffecting = new ParamEffecting();
        this.paramDrawing = new ParamDrawing();

        // console.debug(this.imageWrapper.implementation.app.param.data)
        if (this.imageWrapper.implementation.app.param.data && this.imageWrapper.implementation.app.param.data.drawing) {
            // console.log(this.imageWrapper.implementation.app.param.data)
            const { geometry: geometryWithNoId } = this.imageWrapper.implementation.app.param.data.drawing;
            const geometryWithId = geometryWithNoId.map((geom: any) => geom.id ? geom : ({ ...geom, id: getUUID() }));
            this.paramDrawing.updateDrawingData(geometryWithId);
            this.paramDrawing.scale = this.imageWrapper.implementation.app.param.data.drawing.scale;
        }

        if (this.imageWrapper.implementation.app.param.data && this.imageWrapper.implementation.app.param.data.effecting) {
            const data = this.imageWrapper.implementation.app.param.data.effecting;
            // console.log('from react', data);
            this.paramEffecting.update(data);
        }
    }
    public resetData() {
        this.paramCropping.width = this.imageWrapper.view.widthOrigin; //this.imageWrapper.view.imgOrigin.clientWidth;
        this.paramCropping.height = this.imageWrapper.view.heightOrigin; //this.imageWrapper.view.imgOrigin.clientHeight;
    }
    public updateParamCrop(x: number, y:number, width: number, height: number, scale: number, rotation: number, shapeType: FrameShapeType, aspectRatio: FrameRatioPreset) {
        this.paramCropping.update(x, y, width, height, scale, rotation, shapeType, aspectRatio);
    }
    public updateEffecting(d: any) {
        this.paramEffecting.update(d);
    }
    public updateDrawingData(d: any) {
        this.paramDrawing.updateDrawingData(d);
    }
    public updateDrawingCanvas(canvas: HTMLCanvasElement) {
        this.paramDrawing.updateDrawingCanvas(canvas);
    }
    public getDrawingCanvas() {
        return this.paramDrawing.getDrawingCanvas();
    }
    public clearData() {
        this.initData();
    }
    public updateData(computeImgCtx = false, appClosingBySave = false) {
        this.dataSync(computeImgCtx, appClosingBySave);
        return {
            ctx: this.ctx,
            ctxFiltered: this.ctxFiltered,
            editing: this.paramCropping.toJSON(),
            drawing: this.paramDrawing.toJSON(),
            effecting: this.paramEffecting.toJSON(),
        };
    }
    private dataSync(computeImgCtx = false, appClosingBySave = false) {
        if (this.init) {
            this.imageWrapper.implementation.app.modelController.io.emitAction('done-dataSyn@');
            this.init = false;
        }

        // When you hit the save button after revert to original on markup env, we need to update ctx with no cropped area.
        let isOriginalImage = false;
        if (appClosingBySave && this.imageWrapper.tools.activeApp === ExpressImageAppType.Drawing) {
            isOriginalImage = this.imageWrapper.tools.drawingTool.app.api.needRevertBaseImage();
        }

        const data = this.imageWrapper.tools.editingTool.app.getData(isOriginalImage);

        // to update geometry properties
        this.checkUpdateDrawingData();


        if (computeImgCtx) {
            this.ctx = data.ctx;
            this.ctxFiltered = data.ctxFiltered;
            // console.log(this.paramDrawing.toJSON());
            // console.log(data.params.x, data.params.y)
            // pass up the canvas context to let host app decide when/if/how to use it for exporting image files.
            // no longer need to call toDataURL/getImageData at this level.
            // this.ctxMerged = canvasFilter(ctxMergedData, this.paramEffecting.toJSON(), data.params.x, data.params.y);
            let rPt = this.imageWrapper.implementation.app.param.environment === Environment.AGSM ? getRegularPointAGSM() : getRegularPointSMX();
            let nPt = getNumberedPointSMX();

            this.ctx = contextMergeImgDrawing(this.ctx, this.paramDrawing.toJSON(), this.paramDrawing.scale, data.params.x, data.params.y, this.imageWrapper.implementation.app.param.environment, rPt.path2D, nPt.path2D);
            this.ctxFiltered = contextMergeImgDrawing(this.ctxFiltered, this.paramDrawing.toJSON(), this.paramDrawing.scale, data.params.x, data.params.y, this.imageWrapper.implementation.app.param.environment, rPt.path2D, nPt.path2D);

            if (this.paramCropping.isCropGraphics) {
                this.ctx = cutOutContextByShape(this.ctx, data.params.shape, 0, 0, data.params.width, data.params.height, false);
                this.ctxFiltered = cutOutContextByShape(this.ctxFiltered, data.params.shape, 0, 0, data.params.width, data.params.height, false);
            }
        } else {
            // this.ctxFiltered = undefined;
            // this.ctx = undefined;
        }

        this.paramCropping.update(data.params.x, data.params.y, data.params.width, data.params.height, data.params.scale, data.params.rotationRadian, data.params.shape, data.params.aspectRatio);
    }
    private checkUpdateDrawingData() {
        if (this.imageWrapper.tools.activeApp === ExpressImageAppType.Editing && this.imageWrapper.tools.editingTool.app.api.getGeometryLength() === 0 ) {
            // this.updateDrawingData([]);
        } else {
            const dataDrawing = this.imageWrapper.tools.drawingTool.app.api.getDataGeometry();
            if (dataDrawing) {
                this.updateDrawingData(dataDrawing);
                this.paramDrawing.scale = this.imageWrapper.tools.drawingTool.app.api.getScaleFactor();
            }
        }
    }
}
