import { ExpressImageApp } from '..';
import { ProcessorType, Environment } from '../lib';
import { IO } from './IO';
import { Theme } from './Theme';
import { UI } from './UI';

export class ModelController {
    public app: ExpressImageApp;

    public io: IO;
    public theme: Theme;
    public ui: UI;

    public divHost: HTMLElement;
    public div: HTMLElement;

    public processorType: ProcessorType = ProcessorType.Img;

    constructor(app: ExpressImageApp) {
        this.app = app;
        this.theme = new Theme(this);
        const divHost = document.getElementById(this.app.param.id);
        const div = document.createElement('div');
        if (divHost && div) {
            this.divHost = divHost;
            this.div = div;
            this.div.id = 'express-image-wrapper';
            if(this.app.environment === Environment.Standalone) {
                this.div.style.position = 'relative';
                this.div.style.width = '100vw';
                this.div.style.height = '100vh';
            }
            this.divHost.append(this.div);
        }
        this.init();
    }
    private init() {
        this.io = new IO(this);

        if (this.app.param.ui) {
            this.ui = new UI(this);
        }

        // window.addEventListener('resize', this.resizeByWidow);
    }
    public destroy() {
        // window.removeEventListener('resize', this.resizeByWidow);
    }
}
