export interface ISVGForCanvas {
    type: string;
    width: number;
    height: number;
    path2D: Path2D;
    path?: IctxShape[];
};
export class IctxShape {
    public type: string;
    public pos: number[] = [];
};

export const getObjectFromSVGXML = async (xmlString: string) => {
    const parser = new DOMParser();
    const xml = await parser.parseFromString(xmlString, 'text/xml');
    const d = xmlToJson(xml);
    return d;
};
// eslint:disable-next-line:no-any
export const getCanvaseShapePathFromString = (svg: any, scale: number = 1) => {
    // console.log(svg);
    // const shapePath = shapeString.split(',');
    // this is for edge
    let pathes: string[] = ['M8.5,0', 'C3.806,0', '0,1.817', '0,6.543', 'L0,23.444', 'L8.5,32', 'L17,23.444', 'L17,6.543', 'C17,1.817', '13.194,0', '8.5,0', 'Z', 'M8.5,15.1', 'C5.95949015,15.1', '3.9,13.0405098', '3.9,10.5', 'C3.9,7.95949015', '5.95949015,5.9', '8.5,5.9', 'C11.0405098,5.9', '13.1,7.95949015', '13.1,10.5', 'C13.1002653,11.7200772', '12.6157091,12.8902593', '11.7529842,13.7529842', 'C10.8902593,14.6157091', '9.72007717,15.1002653', '8.5,15.1', 'Z']; // svg.svg.path['@attributes'].d.split(' ');
    // let pathes: string[] = ['M15.44', 'C22.21.37', '27.62', '5.42', '28', '11.73', '28', '17.92', '18.28', '28.46', '15.44', '32', '12.6', '28.46', '4', '17.94', '4', '11.75', '9.31', '0,15.44', 'Z' ] ;
    // let pathes: string[] = ['M15.44', 'C22.21.37', '27.62', '5.42', '28', '11.73', '28', '17.92', '18.28', '28.46', '15.44', '32', '12.6', '28.46', '4', '17.94', '4', '11.75', '9.31', '0,15.44', 'Z' ] ;
    // console.log(pathes);
    let theI: number = 0;
    let ctxShape: IctxShape[] = [];
    for (let i = 0; i < pathes.length; ++i) {
        // console.log(pathes[i]);
        if (pathes[i][0] === 'M') {
            ctxShape[i + ' M'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'C') {
            theI = i;
            ctxShape[theI + ' C'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'L') {
            theI = i;
            ctxShape[theI + ' L'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === '0' || Number(pathes[i][0])) {
            let d = pathes[i].split(',').map(Number);
            ctxShape[theI + ' C'].push(d[0], d[1]);
        }
    }
    // const scaler = (value: string) => parseFloat(value) * scale;

    let region = new Path2D();
    Object.keys(ctxShape).map(key => {
        let g = ctxShape[key];
        if (g) {
            let ii = key.split(' ')[1];
            if (ii === 'M') {
                region.moveTo(g[0] * scale, g[1] * scale);
            } else if (ii === 'C') {
                region.bezierCurveTo(g[0] * scale, g[1] * scale, g[2] * scale, g[3] * scale, g[4] * scale, g[5] * scale);
            } else if (ii === 'L') {
                // console.log(g[0] * scale, g[1] * scale);
                region.lineTo(g[0] * scale, g[1] * scale);
            }
            // console.log(ii);
        }
        return 0
    });
    region.closePath();

    let sVGForCanvas: ISVGForCanvas = {
        type: 'path',
        height: parseInt(svg.svg['@attributes'].height, 10) * scale,
        width: parseInt(svg.svg['@attributes'].width, 10) * scale,
        path: ctxShape,
        path2D : new Path2D(pathPointA),
    };
    return sVGForCanvas;
};
export const getCanvaseShapePathFromStringWithScale = (pathes: string[], scale: number ): Path2D => {
    let theI: number = 0;
    let ctxShape: IctxShape[] = [];
    // const scaler = (value: string) => parseFloat(value) * scale;
    for (let i = 0; i < pathes.length; ++i) {
        // console.log(pathes[i]);
        if (pathes[i][0] === 'M') {
            // ctxShape[i + ' M'] = (pathes[i].slice(1).split(',').map(scaler));
            ctxShape[i + ' M'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'C') {
            theI = i;
            ctxShape[theI + ' C'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'L') {
            theI = i;
            ctxShape[theI + ' L'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === '0' || Number(pathes[i][0])) {
            let d = pathes[i].split(',').map(Number);
            ctxShape[theI + ' C'].push(d[0], d[1]);
        }
    }

    let region = new Path2D();
    Object.keys(ctxShape).map(key => {
        let g = ctxShape[key];
        if (g) {
            let ii = key.split(' ')[1];
            if (ii === 'M') {
                region.moveTo(g[0] * scale, g[1] * scale);
            } else if (ii === 'C') {
                region.bezierCurveTo(g[0] * scale, g[1] * scale, g[2] * scale, g[3] * scale, g[4] * scale, g[5] * scale);
            } else if (ii === 'L') {
                // console.log(g[0] * scale, g[1] * scale);
                region.lineTo(g[0] * scale, g[1] * scale);
            }
            // console.log(ii);
        }
        return 0
    });
    region.closePath();
    return region;
};
