export const getCanvaseShapePathFromOptiSVGXML = (svg: any): ISVGForCanvas | undefined => {
    svg = JSON.parse(JSON.stringify(svg));
    // console.log(svg);
    if (svg.svg && svg.svg.path) {
        let ctxShape: IctxShape[] = [];
        // console.log(svg);
        let pathes: string[] = svg.svg.path['@attributes'].d;
        // console.log(pathes);
        let stringData = '';
        for (let s of pathes) {
        if (s.toLowerCase() === 'c' || s === '-') {
            stringData += ' ';
        }
        if (s.toLowerCase() === 's') {
            stringData += ' ';
        }
        stringData += s;
        }
        pathes = stringData.split(' ');
        let newPath = '';
        for (let i = 0; i < pathes.length; i += 2) {
        newPath += `${pathes[i]},${pathes[i + 1]} `;
        }
        pathes = newPath.split(' ');

        let theI: number = 0;
        for (let i = 0; i < pathes.length; ++i) {
        // console.log(pathes[i]);
        try {
            if (pathes[i][0] === 'M') {
            ctxShape[i + ' M'] = pathes[i]
                .slice(1)
                .split(',')
                .map(Number);
            continue;
            } else if (pathes[i][0] === 'C' || pathes[i][0] === 'c' || pathes[i].includes('c')) {
            theI = i;
            ctxShape[theI + ' C'] = pathes[i]
                .slice(1)
                .split(',')
                .map(Number);
            continue;
            } else if (pathes[i][0] === '0' || Number(pathes[i][0])) {
            let d = pathes[i].split(',').map(Number);
            ctxShape[theI + ' C'].push(d[0], d[1]);
            }
        } catch (error) {
            //
        }
        }
        let region = new Path2D();
        Object.keys(ctxShape).map(key => {
            let g = ctxShape[key];
            if (g) {
                let ii = key.split(' ')[1];
                if (ii === 'M') {
                    region.moveTo(g[0], g[1]);
                } else if (ii === 'C') {
                    region.bezierCurveTo(g[0], g[1], g[2], g[3], g[4], g[5]);
                } else if (ii === 'L') {
                    // console.log(g[0], g[1]);
                    region.lineTo(g[0], g[1]);
                }
                // console.log(ii);
            }
            return 0
        });
        region.closePath();
        // console.log(ctxShape);
        let sVGForCanvas: ISVGForCanvas = {
        type: 'path',
        height: parseInt(svg.svg['@attributes'].height, 10),
        width: parseInt(svg.svg['@attributes'].width, 10),
        path: ctxShape,
        path2D : new Path2D(pathPointA),
        };
        return sVGForCanvas;
    } else if (svg.svg && svg.svg.circle) {
        let ctxShape: IctxShape[] = [];
        // let region = new Path2D();
        /*
        console.log(svg.svg.circle['@attributes']);
        ctxShape.push({ fill: svg.svg.circle['@attributes'].fill });
        ctxShape.push({ stroke: svg.svg.circle['@attributes'].stroke });
    */
        let sVGForCanvas: ISVGForCanvas = {
        type: 'circle',
        height: parseInt(svg.svg['@attributes'].height, 10),
        width: parseInt(svg.svg['@attributes'].width, 10),
        path: ctxShape,
        path2D : new Path2D(pathPointA),
        };
        return sVGForCanvas;
    }
    return undefined;
};
