export const getCanvaseShapePathFromSVGXMLIcon = (svg: any): ISVGForCanvas => {
    svg = JSON.parse(JSON.stringify(svg));
    let ctxShape: IctxShape[] = [];
    let pathes: string[] = svg.svg.g.path['@attributes'].d.split(' ');
    let theI: number = 0;
    // console.log(pathes);
    for (let i = 0; i < pathes.length; ++i) {
        // console.log(pathes[i]);
        if (pathes[i][0] === 'M') {
            ctxShape[i + ' M'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'L' || Number(pathes[i][0])) {
            ctxShape[i + ' L'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === 'Z' || Number(pathes[i][0])) {
            ctxShape[i + ' Z'] = '';
            continue;
        } else if (pathes[i][0] === 'C') {
            theI = i;
            ctxShape[theI + ' C'] = (pathes[i].slice(1).split(',').map(Number));
            continue;
        } else if (pathes[i][0] === '0' || Number(pathes[i][0])) {
            let d = pathes[i].split(',').map(Number);
            ctxShape[theI + ' C'].push(d[0], d[1]);
        }
    }
    let region = new Path2D();
    Object.keys(ctxShape).map(key => {
        let g = ctxShape[key];
        if (g) {
            let ii = key.split(' ')[1];
            if (ii === 'M') {
                region.moveTo(g[0], g[1]);
            } else if (ii === 'C') {
                region.bezierCurveTo(g[0], g[1], g[2], g[3], g[4], g[5]);
            } else if (ii === 'L') {
                // console.log(g[0], g[1]);
                region.lineTo(g[0], g[1]);
            }
            // console.log(ii);
        }
        return 0
    });
    region.closePath();
    // console.log(ctxShape);
    let sVGForCanvas: ISVGForCanvas = {
        type: 'path',
        height: parseInt(svg.svg['@attributes'].height, 10),
        width: parseInt(svg.svg['@attributes'].width, 10),
        path: ctxShape,
        path2D : new Path2D(pathPointA),
    };
    return sVGForCanvas;
};
export const getBtnBySVGPath = (fn: () => void, svgPath: string, scale = 1.0) => {

    let btn = document.createElement('button');
    btn.style.background = 'transparent';
    btn.style.border = 'none';
    // btn.style.fill = color;

    // btn.innerHTML = `
    // <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-cursor-fill" viewBox="0 0 16 16">
    // <path d="M14.082 2.182a.5.5 0 0 1 .103.557L8.528 15.467a.5.5 0 0 1-.917-.007L5.57 10.694.803 8.652a.5.5 0 0 1-.006-.916l12.728-5.657a.5.5 0 0 1 .556.103z"/>
    // </svg>`
    btn.innerHTML = svgPath;
    btn.onclick = () => fn();
    btn.style.transform = `scale(${scale})`;
    return btn;
};
export const getRectSVG = (width: number = 8, height: number = 8, fillet: number = 2, fillStyle: string = '#ffffff', strokeWidth: number = 1, strokeStyle: string = '#CBDAE2' ) => {
    let rect = document.createElementNS('http://www.w3.org/2000/svg', 'rect');
    rect.setAttribute('x','1');
    rect.setAttribute('y','1');
    rect.setAttribute('width', `${width}`);
    rect.setAttribute('height',`${height}`);
    rect.setAttribute('stroke-width', `${strokeWidth}`);
    rect.setAttribute('rx', `${fillet}`);
    rect.setAttribute('fill', `${fillStyle}`);
    rect.setAttribute('stroke', `${strokeStyle}`);


    let rectSVG = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    rectSVG.appendChild(rect);
    rectSVG.setAttribute('width', `${width + strokeWidth * 2}`);
    rectSVG.setAttribute('height', `${height + strokeWidth * 2}`);
    rectSVG.style.position = 'absolute';
    return rectSVG;
};

export interface ISvgPin {
    width: number,
    height: number,
    path2D: Path2D | undefined,
    path: (base: string) => string,
};
const regularPointAGSM: ISvgPin = {
    width: 10,
    height: 10,
    path2D: undefined,
    path: (baseUrl: string) => `njslabSandboxStatic/pointSVG/point.svg`,
};
const regularPointSMX: ISvgPin = {
    width: 10,
    height: 10,
    path2D: undefined,
    path: (baseUrl: string) => `njslabSandboxStatic/pointSVG/point.svg`,
};
const numberedPointSMX: ISvgPin = {
    width: 10,
    height: 10,
    path2D: undefined,
    path: (baseUrl: string) => `njslabSandboxStatic/pointSVG/unnumbered-pin.svg`,
};
export const getRegularPointAGSM = () => {
    return regularPointAGSM;
};

export const getRegularPointSMX = () => {
    return regularPointSMX;
};
export const getNumberedPointSMX = () => {
    return numberedPointSMX;
};
export const importPointAssets = async (baseUrl: string = '') => {
    const pins = [regularPointAGSM, regularPointSMX, numberedPointSMX ];
    const parser = new DOMParser();
    try {
        for (let i = 0 ; i < pins.length; ++i) {
            let svgXML = await loadSVGFromHTTP(pins[i].path(baseUrl));
            if (svgXML) {
                const xmlDoc = parser.parseFromString(svgXML as any,'text/xml');
                const obj = xml2json(xmlDoc);
                pins[i].width = obj['attributes']['height'];
                pins[i].height = obj['attributes']['width'];
                pins[i].path2D = new Path2D(obj['children'][0]['attributes']['d']);
            }
        }
        return true;
    } catch (error) {
        console.debug(error);
        return false;
    }
};
