import { ImageTools } from '..';
export class DrawingTool {
    public tools: ImageTools;
    public app: any;
    public revertToOriginal = false;
    constructor(tools: ImageTools) {
        this.tools = tools;
    }
    public async append() {
        return import(`../../../../../AppImageDrawing`).then(({ AppImageDrawing, InteractionMode }) => {
            // return import(`../../../../../AppImageDrawing`).then(({ AppImageDrawing, InteractionMode }) => {
            this.tools.divUpdatePre();

            const params: any = {
                id : this.tools.imageWrapper.implementation.app.modelController.divHost.id,
                UI : this.tools.imageWrapper.implementation.app.param.ui,
                interactionMode: InteractionMode.Drawing,
                config: {
                    BASE_URL : '',
                },
                environment: this.tools.imageWrapper.implementation.app.param.environment,
                themeUI: this.tools.imageWrapper.implementation.app.modelController.theme.themeData,
            };

            // for revert to original
            if (this.revertToOriginal) {
                this.tools.imageWrapper.dataSet.paramDrawing.updateDrawingData([]);
                this.revertToOriginal = false;
            }
            const geo = this.tools.imageWrapper.dataSet.paramDrawing.toJSON();
            // console.log(geo);
            // console.log('init', this.tools.imageWrapper.dataSet.paramDrawing.getDrawingData())
            this.app = new AppImageDrawing(params, geo);
            this.app.updateEffecting(this.tools.imageWrapper.dataSet.paramEffecting.toJSON());
            this.app.emitAction = (data: any) => {
                this.from(data);
            };
            this.app.initImageByImg(
                this.tools.imageWrapper.implementation.imageWrapper.view.img,
                this.tools.imageWrapper.implementation.imageWrapper.dataSet.paramCropping.toString(),
                this.tools.imageWrapper.implementation.imageWrapper.view.imgOrigin,
                this.tools.imageWrapper.implementation.app.param.colorCover,
                );

        }).catch((err) => {
            console.debug('Stop loading Image Editing App.');
            console.debug(err);
            throw err;
        });
    }
    public from(dataIn: any) {
        const index = dataIn.indexOf(this.tools.delimiter);
        const action = index < 0 ? [dataIn] : [dataIn.substring(0, index), dataIn.substring(index + 1, dataIn.length)];
        let param;
        // console.log('from drawing', dataIn);
        // console.trace()
        // console.log('action', action[0], action[1]);
        switch (action[0]) {
            case 'set-drawing-mode':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'update-container-size':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'destroy':
                this.tools.appDestroy();
                return; // we do not need to update any info from the drawing tool
            case 'ready':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'select-shapes':
                // console.log(dataIn)
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'history':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'update':
                this.tools.imageWrapper.dataSet.updateDrawingData(JSON.parse(action[1]));
                break;
            case 'remove':
                this.updateGeomData();
                break
            case 'add':
                this.updateGeomData();
                break;
            case 'update-cropping':
                param = JSON.parse(action[1]);
                // console.log('update-cropping', param);
                this.updateCropping(param);
                break;
            case 'update-effecting':
                param = JSON.parse(action[1]);
                // console.log('update-effecting', param);
                this.tools.imageWrapper.dataSet.updateEffecting(param)
                break;
            case 'transform':
                // console.trace()
                this.updateGeomData();
                break;
            default:
                break;
        }
        // console.log('update', this.tools.imageWrapper.dataSet.paramDrawing.getDrawingData())
    }
    private updateCropping(param: any) {
        this.tools.imageWrapper.dataSet.updateParamCrop (
            param.x, param.y, param.width, param.height, param.scale, param.rotation, param.shape, param.aspectRatio
        );
        this.tools.editingTool.app.api.updateFrameParam (
            param.x, param.y, param.width, param.height, param.scale, param.rotation, param.shape, param.aspectRatio
        );
        this.tools.imageWrapper.implementation.app.modelController.io.emitAction(`ready@update-div-size`);
    }
    private updateGeomData() {
        const dataGeo = this.app.api.getAllGeometries();
        const dataScale = this.app.api.getScaleFactor();
        // console.log('updateGeomData',dataGeo)
        this.tools.imageWrapper.dataSet.paramDrawing.updateDrawingData(dataGeo);
        this.tools.imageWrapper.dataSet.paramDrawing.scale = dataScale;
        // console.log(this.tools.imageWrapper.dataSet.paramDrawing.scale)
        this.tools.imageWrapper.dataSet.updateDrawingCanvas(this.app.api.getCanvas());
        // console.log(this.tools.imageWrapper.dataSet);
        // console.trace();
    }
}
