import { ProcessorType  } from '../../../lib';
import { ImageWrapper } from '..';
export class View {
    public imageWrapper: ImageWrapper;
    public imgOrigin: HTMLImageElement;
    private _img: HTMLImageElement;
    public widthOrigin = 0;
    public heightOrigin = 0;
    public divImg: HTMLElement;

    constructor(imageWrapper: ImageWrapper) {
        this.imageWrapper = imageWrapper;

    }
    public updateSizeDiv(v: number) {
        if (this.imageWrapper.implementation.app.modelController.processorType === ProcessorType.Img) {
            this.divImg.style.width = `${v}%`;
        }
    }
    public resetImageSize() {
        this.imgOrigin.width = this.widthOrigin;
        this.imgOrigin.height = this.heightOrigin;
    }
    public initDivForImgModified(img: HTMLImageElement) {
        this.widthOrigin = img.width;
        this.heightOrigin = img.height;
        // this.img.style.objectFit = 'none';

        // img.style.width = '100%';
        // img.style.height = '100%';
        this.imgOrigin.style.maxWidth = '100%';
        this.imgOrigin.style.maxHeight = '100%';

        // margin: auto;
        // display: block;

        // this.expressImageData.paramCropping.x = 10;
        // this.expressImageData.paramCropping.y = 10;

        // this.img.style.position = 'absolute';
        // this.img.style.zIndex = '-1';
        // this.img.style.overflow = 'hidden';
        // this.img.style.textAlign = 'center';
        // if (this.implementation.appImageCropping) {
        //     this.implementation.appImageCropping.initImageByImg(val as HTMLImageElement);
        // }
        // if (this.implementation.appImageAnno) {
        //     this.implementation.appImageAnno.updateImage(val as HTMLImageElement);
        // }
        const divImgContainer = document.createElement('div');
        divImgContainer.style.textAlign = 'center';
        divImgContainer.style.display = 'flex';
        divImgContainer.style.justifyContent = 'center';
        this.imageWrapper.implementation.app.modelController.div.append(divImgContainer);

        const divImg = document.createElement('div');
        // divCenter.style.textAlign = 'center';
        divImg.id = 'divImg'
        divImg.style.width = '50%';
        divImg.style.display = 'flex';
        divImg.style.justifyContent = 'center';
        divImg.append(this.imgOrigin);
        this.divImg = divImg;
        divImgContainer.append(this.divImg);
    }
    public get img() {
        return this._img;
    }
    public set img(img: HTMLImageElement) {
        this._img = img;
    }
    public initDivForCSS(img: HTMLImageElement) {
        this.widthOrigin = img.width;
        this.heightOrigin = img.height;
        this.img.style.objectFit = 'none';

        // this.expressImageData.paramCropping.x = 10;
        // this.expressImageData.paramCropping.y = 10;

        // this.img.style.position = 'absolute';
        // this.img.style.zIndex = '-1';
        // this.img.style.overflow = 'hidden';
        // this.img.style.textAlign = 'center';
        // if (this.implementation.appImageCropping) {
        //     this.implementation.appImageCropping.initImageByImg(val as HTMLImageElement);
        // }
        // if (this.implementation.appImageAnno) {
        //     this.implementation.appImageAnno.updateImage(val as HTMLImageElement);
        // }
        const divImg = document.createElement('div');
        divImg.style.textAlign = 'center';


        divImg.append(img);
        this.imageWrapper.implementation.app.modelController.div.append(divImg);

    }
    public updateImage(img: HTMLImageElement) {
        this.divImg.removeChild(this.img);
        this.img = img;

        this.img.style.width = '100%';
        this.img.style.height = '100%';


        this.divImg.append(this.img);

        // console.log('from ddd', this.img.clientWidth, this.img.clientHeight)
        // console.log(this.img.getBoundingClientRect())
        // console.log(this.img)

    }
    public clear() {
        this.clearData();
        this.clearImgElement();
    }
    public clearData() {
        this.imageWrapper.dataSet.clearData();
    }
    public clearImgElement() {
        if (this.img && this.divImg) {

            try {
                this.divImg.removeChild(this.img);
                this.imageWrapper.tools.exImageViewer.destroy();
            } catch (error) {
                console.debug('fail to remove img and img div');
            }

        }
    }

}
