import { ExpressImageApp } from './AppExpressImage/ExpressImage';
import { ExpressImageAppType } from './lib/Enum/EnumExpressImage';
import { ImageUI } from './UIComponent/ImageUI';

export class ImageAnnoApp {
    static init(id: string) {
        new ImageAnnoApp(id);
    }
    public div: HTMLDivElement;
    public host: HTMLDivElement;
    public imageUI: ImageUI;
    public app: ExpressImageApp;
    constructor(id: string) {
        this.host = document.getElementById(id) as HTMLDivElement; 
        this.host.style.backgroundColor = '#999';
        // this.host.style.display = 'flux';

        this.imageUI = new ImageUI(this, this.host);

        this.init(this.imageUI.divContainer.id);
    }
    public init(id: string) {
        this.app = new ExpressImageApp({
            id,
            ui: false,
            appDefault : ExpressImageAppType.Editing,
            data: {},
            emitAction: this.emit,
        });
        // this.app.importImgByURL('https://raw.githubusercontent.com/NamjuLee/data/master/img/bg/zoom-James-River-Kayaking.jpg', true);
        this.app.importImgByURL('https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram.jpg', true);
    }
    public emit = (d: string) => {
        // console.log(d);
    }
}


export class Solution {
    public divHost: HTMLElement;

    constructor(id: string) {
        this.divHost = document.getElementById(id);

        ImageAnnoApp.init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}