import { getBtnBySVGPath, Environment } from '../../lib';
import { ModelController } from '..';
export class UI {
    public modelController: ModelController;
    constructor(controller: ModelController) {
        this.modelController = controller;
        this.init();
    }
    public init() {
        let divMenu = document.createElement('div');
        divMenu.id = 'expree-image-ui';
        this.modelController.div.append(divMenu);

        const divURL = document.createElement('input');
        divURL.style.color = '#ffffff';
        divURL.style.background = 'rgba(255, 255, 255,0.1)';
        divURL.style.border = '0px';
        divURL.style.margin = '2px';
        // divURL.textContent = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.png';
        divURL.setAttribute('type', 'text');
        divURL.setAttribute('value', 'https://raw.githubusercontent.com/NamjuLee/data/master/img/building%20footprint.png');
        divMenu.append(divURL);
        // divURL.oninput = () => {
        //     // this.app.api.inputImportByURL(divURL.textContent as string);
        // };
        divURL.addEventListener('keyup', (event: KeyboardEvent) => {
            if (event.keyCode === 13) { // Enter
                if (this.modelController.app.environment === Environment.Standalone) {
                    event.preventDefault();
                }
                // console.log(divURL.value as string);
                this.modelController.app.api.onClickImportImgByURL(divURL.value);
            }
        });

        let colorText = '#ffffff';

        let divTitle = document.createElement('div');
        divTitle.textContent = 'XGraphics Express Image App';
        divTitle.style.fontSize = '14px';
        divTitle.style.fontWeight = '400';
        divTitle.style.color = '#000000';
        // divTitle.style.fontWeight = 'bold';
        divTitle.style.padding = '10px';
        divMenu.append(divTitle);

        let divEditing = document.createElement('div');
        divEditing.textContent = 'Editing';
        divEditing.style.fontSize = '12px';
        divEditing.style.fontWeight = '400';
        divEditing.style.color = colorText;
        divEditing.style.width = '220px';
        divEditing.style.padding = '5px';
        divEditing.style.marginBottom = '10px';
        divEditing.onclick = () => {
            this.modelController.app.api.onClickAppendApp(`editing`);
        };
        divEditing.onmouseenter = () => {
            divEditing.style.background = '#333333';
        }
        divEditing.onmouseleave = () => {
            divEditing.style.background = 'rgba(0, 0, 0, 0)';
        }
        divMenu.append(divEditing);

        let divEffecting = document.createElement('div');
        divEffecting.textContent = 'Effecting';
        divEffecting.style.fontSize = '12px';
        divEffecting.style.fontWeight = '400';
        divEffecting.style.color = colorText;
        divEffecting.style.width = '220px';
        divEffecting.style.padding = '5px';
        divEffecting.style.marginBottom = '10px';
        divEffecting.onclick = () => {
            this.modelController.app.api.onClickAppendApp(`effecting`);
        };
        divEffecting.onmouseenter = () => {
            divEffecting.style.background = '#333333';
        }
        divEffecting.onmouseleave = () => {
            divEffecting.style.background = 'rgba(0, 0, 0, 0)';
        }
        divMenu.append(divEffecting);

        let divDrawing = document.createElement('div');
        divDrawing.textContent = 'Drawing';
        divDrawing.style.fontSize = '12px';
        divDrawing.style.fontWeight = '400';
        divDrawing.style.color = colorText;
        divDrawing.style.width = '220px';
        divDrawing.style.padding = '5px';
        divDrawing.style.marginBottom = '10px';
        divDrawing.onclick = () => {
            this.modelController.app.api.onClickAppendApp(`drawing`);
        };
        divDrawing.onmouseenter = () => {
            divDrawing.style.background = '#333333';
        }
        divDrawing.onmouseleave = () => {
            divDrawing.style.background = 'rgba(0, 0, 0, 0)';
        }
        divMenu.append(divDrawing);

        let imgElementSize = document.createElement('input');
        imgElementSize.type = 'range';
        imgElementSize.min = '0.1';
        imgElementSize.max = '1.0';
        imgElementSize.step = '0.01';
        imgElementSize.value = '0.5';
        imgElementSize.oninput = () => {
            this.modelController.app.api.resizeDiv(+imgElementSize.value);
        };
        divMenu.append(imgElementSize);


        let colorFill = this.modelController.theme.themeData.top.iconFill;

        let btn = getBtnBySVGPath(
            () => {
                this.modelController.app.api.onToggleInteractionMode();
            },         `
            <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="${colorFill}" viewBox="0 0 12 32">
            <path fill-rule="evenodd" d="M0 5a2 2 0 0 1 2-2h7.5a2 2 0 0 1 1.983 1.738l3.11-1.382A1 1 0 0 1 16 4.269v7.462a1 1 0 0 1-1.406.913l-3.111-1.382A2 2 0 0 1 9.5 13H2a2 2 0 0 1-2-2V5z"/>
            </svg>
            `
        )
        btn.style.display = 'block';
        btn.style.marginTop = '15px';
        btn.style.transform = 'scale(2)';
        const svg =  btn.firstElementChild as SVGElement;
        svg.onmouseenter = () => {
            svg.style.fill = '#333333';
        }
        svg.onmouseleave = () => {
            svg.style.fill = 'rgba(255, 255, 255, 1)';
        }
        divMenu.append(btn);

        let btnDownload = getBtnBySVGPath(
            () => {
                this.modelController.app.api.onClickDownload();
            },         `
            <svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="${colorFill}" viewBox="0 0 12 32">
            <path d="M8 2a5.53 5.53 0 0 0-3.594 1.342c-.766.66-1.321 1.52-1.464 2.383C1.266 6.095 0 7.555 0 9.318 0 11.366 1.708 13 3.781 13h8.906C14.502 13 16 11.57 16 9.773c0-1.636-1.242-2.969-2.834-3.194C12.923 3.999 10.69 2 8 2zm2.354 6.854-2 2a.5.5 0 0 1-.708 0l-2-2a.5.5 0 1 1 .708-.708L7.5 9.293V5.5a.5.5 0 0 1 1 0v3.793l1.146-1.147a.5.5 0 0 1 .708.708z"/>
            </svg>
            `
        )
        btnDownload.style.display = 'block';
        btnDownload.style.marginTop = '15px';
        btnDownload.style.transform = 'scale(2)';
        const svgDownload =  btnDownload.firstElementChild as SVGElement;
        svgDownload.onmouseenter = () => {
            svgDownload.style.fill = '#333333';
        }
        svgDownload.onmouseleave = () => {
            svgDownload.style.fill = 'rgba(255, 255, 255, 1)';
        }
        divMenu.append(btnDownload);

    }

}
