export const hex2CanvasColor = (hex: string, visibility: number | string = 0, isGray: boolean = false)  => {
  // if (hex[0] !== '#') {
  //     const col = hex.split('(')[1].split(')')[0].split(',');
  //     hex = RGBToHex(parseInt(col[0]), parseInt(col[1]), parseInt(col[2]));
  // }
  // Convert #AA77CC to rbga() format for Firefox
  let thecol;
  visibility = visibility || '1.0';
  hex = hex.replace('#', '');
  const r = parseInt(hex.substring(0, 2), 16);
  const g = parseInt(hex.substring(2, 4), 16);
  const b = parseInt(hex.substring(4, 6), 16);
  if (isGray) {
      // const v = (0.58 * (r / 255)) + (0.17 * (g / 255)) + (0.8 * (b / 255));
      const v = (0.58 * r) + (0.17 * g) + (0.8 * b);
      thecol = 'rgba(' + v + ',' + v + ',' + v + ',' + visibility + ')';
  } else {
      thecol = 'rgba(' + r + ',' + g + ',' + b + ',' + visibility + ')';
  }
  return thecol;
};
