// https://graphicdesign.stackexchange.com/questions/83866/generating-a-series-of-colors-between-two-colors
export const interpolateColor = (colA: number[], colB: number[], factor: number = 0.5): number[] => {
  const result = [colA[0], colA[1], colA[2]]; // ;colA.slice();
  for (let i = 0; i < 3; i++) {
      // result[i] = Math.round(result[i] + factor * (colB[i] - colA[i]));
      result[i] = (result[i] * 255 + factor * (colB[i] * 255 - colA[i] * 255));
  }
  // console.log(result);
  return result;
};
export const interpolateColorsByArray = (cA: number[], cB: number[], steps: number): number[][] => {
  const step = 1. / (steps - 1.), interpolatedColors: number[][] = [];
  for (let i = 0; i < steps; i++) {
      interpolatedColors.push(interpolateColor(cA, cB, step * i));
  }
  // console.log(interpolatedColors);
  return interpolatedColors;
};
export const interpolateColors = (rgb0: string, rgb1: string, steps: number): number[][] => {
  const step = 1 / (steps - 1), interpolatedColors: number[][] = [];

  const cA = rgb2array(rgb0);
  const cB = rgb2array(rgb1);

  if (cA && cB) {
      for (let i = 0; i < steps; i++) {
          interpolatedColors.push(interpolateColor(cA, cB, step * i));
      }
  }
  return interpolatedColors;
};
