export const linearColorInterpolation = (c0: number[], c1: number[], interpolation: number = 0.5) => {
  const cOut = [];
  for (let i = 0 ; i < c0.length; ++i) {
      const c = c0[i] + ((c1[i] - c0[i]) * interpolation);
      cOut.push(c);
  }
  return cOut;
};
