export const blend = (vA: number, vB: number, blend: number) => {
  return vA * (1.0 - blend) + vB * blend;
};
export const addition = (vA: number, vB: number, blend: number) => {
  return vA * (1.0 - blend) + blend * (vA + vB) * 0.5;
};
export const subtraction = (vA: number, vB: number, blend: number) => {
  return vA * (1.0 - blend) + blend * (vA - vB) * 2;
};
export const multiply = (vA: number, vB: number, blend: number) => {
  return vA * (1.0 - blend) + blend * (vA * vB) * 0.5;
};
export const screen = (vA: number, vB: number, blend: number) => {
  return vA * (1.0 - blend) + blend * (1 - (1 - vA) * (1 - vB));
};
export const overlay = (vA: number, vB: number, blend: number) => {
  let val = 0;
  if (vA < 0.5) { val = 2 * vA * vB; } else { val = 1 - 2 * (1 - vA) * (1 - vB); }
  val = vA * (1.0 - blend) + blend * val;
  return val;
};
export const softLight = (vA: number, vB: number, blend: number)  => {
  let val = 0;
  if (vB < 0.5) { val = 2 * vA * vB + vA * vA * (1 - 2 * vB); } else { val = 2 * vA * (1 - vB) + Math.sqrt(vA) * (2 * vB - 1); }
  val = vA * (1.0 - blend) + blend * val;
  return val;
};
