export const hex2hsvByHue = (hex: string, s: number) => { // s 0 to 1
  const col = hex2rgba(hex);
  const hsv = rgb2hsv(col[0], col[1], col[2]);
  hsv[1] = s;
  return hsva2rgb(hsv[0], hsv[1], hsv[2], 1);

};
export const hex2hsvByCol = (hex: string, s: number) => { // s 0 to 1
  const col = hex2rgba(hex);
  const hsv = rgb2hsv(col[0], col[1], col[2]);
  hsv[0] += s;
  return hsva2rgb(hsv[0], hsv[1], hsv[2], 1);

};
