// from -1 to 1 for brightness and contrast
export const brightnessContrastLUT = (brightness: number, contrast: number): number[] => {
  const lut = [];
  const contrastAdjust = -128 * contrast + 128;
  const brightnessAdjust = 255 * brightness;
  const adjust = contrastAdjust + brightnessAdjust;
  for (let i = 0; i < 256; i++) {
      const c = i * contrast + adjust;
      const result = Math.floor(c < 0 ? 0 : (c > 255 ? 255 : c));
      lut.push(result);
  }
  return lut;
};
export const imgBrightnessContrast = (img: ImageData, brightness: number, contrast: number): ImageData => {
  const imgOut = new ImageData(img.width, img.height);
  const lut = brightnessContrastLUT(brightness, contrast + 1.0);
  for (let i = 0; i < img.data.length; i += 4) {
          const r = lut[img.data[i + 0]];
          const g = lut[img.data[i + 1]];
          const b = lut[img.data[i + 2]];
          const a = img.data[i + 3];

          imgOut.data[i + 0] = r; imgOut.data[i + 1] = g; imgOut.data[i + 2] = b; imgOut.data[i + 3] = a;

  }
  return imgOut;
};
export const hueSaturationLightness = (img: ImageData, Hue: number, Saturation: number, Lightness: number) => {
  const imgOut = new ImageData(img.width, img.height);
  for (let i = 0; i < img.data.length; i += 4) {
      const r = img.data[i + 0];
      const g = img.data[i + 1];
      const b = img.data[i + 2];
      const a = img.data[i + 3];

      const hls = rgb2hsl(r , g , b );
      // console.log(hls);
      hls[0] += Hue;

      hls[1] += Saturation;
      if (hls[1] < 0) {hls[1] = 0; }

      hls[2] += Lightness;

      const col = hsl2rgb(hls[0], hls[1], hls[2]);
      // imgOut.SetPixel(x, y, Color.FromArgb(255, (int)(col[0] * 255), (int)(col[1] * 255), (int)(col[2] * 255)));

      imgOut.data[i + 0] = col[0];
      imgOut.data[i + 1] = col[1];
      imgOut.data[i + 2] = col[2];
      imgOut.data[i + 3] = a;
  }
  return imgOut;
};
export const rgbaHueSaturationLightness = (col: number[], Hue: number, Saturation: number, Lightness: number ) => {
      const hls = rgb2hsl(col[0], col[1], col[2]);
      // console.log(hls);
      hls[0] += Hue;

      hls[1] += Saturation;
      if (hls[1] < 0) {
      hls[1] = 0;
      }

      hls[2] += Lightness;

      const colOut = hsl2rgb(hls[0], hls[1], hls[2]);
      return [colOut[0], colOut[1], colOut[2], col[3]];
};
export const imgInvert = (img: ImageData) => {
  const imgOut = new ImageData(img.width, img.height);
  for (let i = 0; i < img.data.length; i += 4) {
      const r = img.data[i + 0];
      const g = img.data[i + 1];
      const b = img.data[i + 2];

      imgOut.data[i + 0] = 255 - r;
      imgOut.data[i + 1] = 255 - g;
      imgOut.data[i + 2] = 255 - b;
      imgOut.data[i + 3] = 255;
  }
  return imgOut;
};
export const imgThreshold = (img: ImageData, thresholdValue: number, high: number, low: number) => {
  const imgOut = new ImageData(img.width, img.height);
  for (let i = 0; i < img.data.length; i += 4) {
      const r = img.data[i + 0];
      const g = img.data[i + 1];
      const b = img.data[i + 2];

      const v = threshold(r / 255.0, g / 255.0, b / 255.0, thresholdValue, high, low);
      let gray = Math.floor(v * 255);
      gray = (gray < 255) ? gray : 255;
      gray = (gray < 0) ? 0 : gray;

      // imgOut.SetPixel(x, y, Color.FromArgb(255, gray, gray, gray));
      imgOut.data[i + 0] = gray;
      imgOut.data[i + 1] = gray;
      imgOut.data[i + 2] = gray;
      imgOut.data[i + 3] = 255;
  }
  return imgOut;
};
