import { InteractionMode, canvasMergeImg, downloadImage } from '../../lib';
import { Implementation } from '..';
export class CommonProcess {
    public implementation: Implementation;
    constructor(implementation: Implementation) {
        this.implementation = implementation;

    }
    public downloadImage() {
        let canvasSaving = this.implementation.imageWrapper.tools.exImageViewer.app.getCanvas();
        let img = this.implementation.imageWrapper.tools.editingTool.app.getImage();
        if (canvasSaving) {

            let imgSaving = new Image();
            // imgSaving = this.implementation.imageWrapper.view.img;
            imgSaving = img;
            // imgSaving.style.width = '100px'; // canvasSaving.width;
            // imgSaving.style.height = '100px'; //canvasSaving.height;
            // console.log(this.implementation.imageWrapper.view.img);

            img = canvasMergeImg(imgSaving, canvasSaving, imgSaving.width, imgSaving.height);
        }

        downloadImage(img);
    }
    public toggleInteractionMode() {
        if (this.implementation.imageWrapper.tools.exImageViewer.app) {
            if (this.implementation.imageWrapper.tools.exImageViewer.app.renderer.mode === InteractionMode.InteractiveViewer) {
                this.implementation.imageWrapper.tools.exImageViewer.app.renderer.mode = InteractionMode.ObserveViewer;
            } else {
                this.implementation.imageWrapper.tools.exImageViewer.app.renderer.mode = InteractionMode.InteractiveViewer;
            }
        }
    }
}

