import { filterPreset } from './FilterPreset';
import { linearInterpolate } from '../Utility/MathUtility';
import { Filters } from '../Enum/EnumExpressImage';
export const imageFilterByArray = (ctx: CanvasRenderingContext2D, img: HTMLCanvasElement | HTMLImageElement, filters: any, interpolation = 1.0) => {
    if (filters === undefined) { return ctx; }
    ctx.filter = getFiltersString(filters, interpolation);
    ctx.drawImage(img, 0, 0);
    ctx.filter = 'none';
    return ctx;
}
export const getFiltersString = (filters: any, interpolation = 1.0) => {
    let filter = ''
    for (let i = 0 ; i < filters.length; ++i) {
        filter += getFilterString(filters[i][0], filters[i][1], interpolation)
    }
    return filter;
}
const getFilterString = (key: string, value: number, interpolation = 1.0) => {
    switch (key as Filters) {
        case Filters.blur:
            return `blur(${linearInterpolate(0, value, interpolation)}px)`
        case Filters.sepia:
            return `sepia(${linearInterpolate(0, value, interpolation)})`
        case Filters.contrast:
            return `contrast(${linearInterpolate(1, value, interpolation)})`
        case Filters.brightness:
            return `brightness(${linearInterpolate(1, value, interpolation)})`
        case Filters.saturate:
            return `saturate(${linearInterpolate(1, value, interpolation)})`
        case Filters.hueRotate:
            return `hue-rotate(${linearInterpolate(0, value, interpolation)}deg)`
        default:
            return '';
    }
}
