import { FrameShapeType, ImageType, ExpressImageAppType, ISVGPackageForCanvas, Environment, importPointAssets, } from './lib';
import { API } from './API';
import { Implementation } from './Implementation';
import { ModelController } from './ModelController';
export interface IExpressImageAppParam {
    id: string;
    url?: string;
    ui?: boolean;
    appDefault?: ExpressImageAppType;
    colorCover?: string;
    debug?:boolean;
    data?: any;
    graphicShape?: ISVGPackageForCanvas;
    environment?: Environment;

    emitAction?: (data: string) => void;
    /** @default ImageType.Jpeg */
    typeImageCompression?: ImageType;
}
export interface DataCropping {
    src: string;
    widthCropped: number;
    heightCropped: number;
    shape: FrameShapeType;
    x: number;
    y: number;
}
export class ExpressImageApp {
    public api: API;
    public modelController: ModelController;
    public implementation: Implementation;

    public debugMode = true;
    public typeImageCompression: ImageType;
    public param: IExpressImageAppParam;
    public emitAction: (data: string) => void;
    private _environment: Environment = Environment.SMX;
    constructor(param: IExpressImageAppParam) {
        console.log('init.....');
        console.log(param);
        importPointAssets();
        this.param = param;
        this.typeImageCompression = param.typeImageCompression || ImageType.Jpeg;
        if (param.emitAction) {
            this.bindActionEmitting(param.emitAction);
        }
        this.initApp();
    }
    private initApp() {
        this.api = new API(this);
        if (this.param.environment) {
            this.environment = this.param.environment;
        }
        this.modelController = new ModelController(this);
        this.implementation = new Implementation(this);
        if (this.param.url) {
            this.importImgByURL(this.param.url);
        }
        // console.log('init app')
        // for debugging purpose
        document.body.addEventListener('keydown', (k: KeyboardEvent) => {
            // console.log(k)
            // console.trace();

            if (k.shiftKey && k.key === 'KeyD') {
                // console.log(this.api.getData());
            } else if (k.shiftKey && k.key === '{') {
                this.api.undo('');
            } else if (k.shiftKey && k.key === '}') {
                this.api.redo('');
            } else if (k.shiftKey && k.key === '+') {
                this.api.rotateCropping(-10)
            } else if (k.shiftKey && k.key === '_') {
                this.api.rotateCropping(10)
            } else if (k.shiftKey && k.key === ')') {
                this.api.zoomCropping(-0.13)
            } else if (k.shiftKey && k.key === '(') {
                this.api.zoomCropping(0.13)
            } else if (k.shiftKey && k.key === '!') {
                this.api.scaleDownGraphics()
            } else if (k.shiftKey && k.key === '@') {
                this.api.scaleUpGraphics()
            }

            if (k.key === 's') {
                this.environment = this.environment === Environment.AGSM ? Environment.SMX : Environment.AGSM
              }
        });

    }
    public get environment() {
        return this._environment;
    }
    public set environment(v: Environment) {
        this._environment = v;
        this.param.environment = v;
        this.implementation.imageWrapper.updateEnvironment(v)
    }
    public postUpdate() {
        console.log('post update...', this.param.appDefault)
        if (this.param.appDefault === ExpressImageAppType.Effecting ) {
            this.pushAction('crop', 'tab-change', 'filter')
        } else if (this.param.appDefault === ExpressImageAppType.Drawing ) {
            this.pushAction('crop', 'tab-change', 'markup')
        } else if (this.param.appDefault === ExpressImageAppType.Editing ) {
            // do nothing because it is the default app
        }
    }
    public pushAction(app: any, command: any, parameter: any) {
        this.modelController.io.pushAction(app, command, parameter);
    }

    public bindActionEmitting(emitAction: (action: string) => void) {
        this.emitAction = emitAction;
    }

    public getData(computeImgCtx = false, appClosingBySave = false) {
        return this.api.getData(computeImgCtx, appClosingBySave);
    }
    public destroy() {
        this.api.destroy();
    }
    public async importImgByURL(url: string = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/bg/zoom-James-River-Kayaking.jpg', needEditingApp: boolean = false) {
        return await this.api.onClickImportImgByURL(url, needEditingApp);
    }
}
