export class ParamEffecting {
    private data = {
        typePreset : 'normal',
        filters: [
            ['blur', 0],
            ['sepia', 0],
            ['contrast', 1],
            ['brightness', 1],
            ['saturate', 1],
            ['hue-rotate', 0]
        ],
        // @ts-ignore
        tints : [],
        inter: 1,
    };
    public update(d: any) {
        // console.log('from model', d);
        this.data.typePreset = d.typePreset;
        this.data.filters = d.filters;
        this.data.tints = d.tints;

        // console.log('data', this);
    }
    // public updateTypePreset(d: any) {
    //     console.log(d);
    //     // this.data.typePreset = d;
    //     // console.log(d);
    //     // this.data.typePreset = typePreset;
    //     // this.data.filter = filterPreset[typePreset].filter;
    //     // this.data.tint = filterPreset[typePreset].tint;
    // }
    public toJSON() {
        return {
            typePreset: this.data.typePreset,
            filters: this.data.filters,
            tints: this.data.tints,
            inter: 1,
        };
    }
}
