export class ParamDrawing {
    public scale = 1;
    public canvas: HTMLCanvasElement;
    private data: any;
    public updateDrawingData(data: any) {
        // console.trace();
        // console.log(JSON.parse(JSON.stringify(data)).length);
        this.data = data;
        // console.log(this.data)
    }
    public toJSON() {
        return {
            scale : this.scale,
            geometry: this.data
        };
    }
    public updateDrawingCanvas(canvas: HTMLCanvasElement) {
        this.canvas = canvas;
    }
    public getDrawingCanvas() {
        return this.canvas;
    }
}
