import { ImageAnnoApp } from '..';
import { ACTIVECOMMAND_MODE } from '../lib';
export class ImageUI {
    imageAnnoApp: ImageAnnoApp;
    hostDiv: HTMLDivElement;
    main: HTMLDivElement;
    divTop: HTMLDivElement;
    divContainer: HTMLDivElement;
    constructor(imageAnnoApp: ImageAnnoApp, hostDiv: HTMLDivElement) {
        this.imageAnnoApp = imageAnnoApp;
        this.hostDiv = hostDiv;
        
        this.main = document.createElement('div') as HTMLDivElement;
        this.main.id = 'imageMain';
        this.main.style.zIndex = '10';
        this.main.style.backgroundColor = '#777';
        this.main.style.width = '100%';
        this.main.style.height = '100%';
        this.main.style.position = 'absolute';
        this.hostDiv.appendChild(this.main);


        this.divContainer = document.createElement('div') as HTMLDivElement;
        this.divContainer.id = 'ng';
        // this.divContainer.style.left = '200px';
        // this.divContainer.style.position = 'absolute';
        this.main.appendChild(this.divContainer);
        // this.div.style.background = 'fff';
        // this.div.style.width = '80%';
        // this.div.style.height = '80%';


        this.divTop = document.createElement('div') as HTMLDivElement;
        this.divTop.id = 'ui';
        this.divTop.style.zIndex = '10';
        this.divTop.style.backgroundColor = '#000';
        this.divTop.style.width = '100%';
        this.divTop.style.height = '100px';
        this.divTop.style.position = 'absolute';
        this.main.appendChild(this.divTop);

        this.appendButton();
    }
    appendButton() {
        let divEditing = document.createElement('div');
        divEditing.textContent = 'RPoint';
        divEditing.style.fontSize = '12px';
        divEditing.style.fontWeight = '400';
        divEditing.style.color = '#fff';
        divEditing.style.width = '220px';
        divEditing.style.padding = '5px';
        divEditing.style.marginBottom = '10px';
        divEditing.onclick = () => {
            this.imageAnnoApp.app.pushAction('markup', 'set-drawing-mode', ACTIVECOMMAND_MODE.POINT_REGULAR);
        };
        this.divTop.appendChild(divEditing);

        divEditing = document.createElement('div');
        divEditing.textContent = 'Rect';
        divEditing.style.fontSize = '12px';
        divEditing.style.fontWeight = '400';
        divEditing.style.color = '#fff';
        divEditing.style.width = '220px';
        divEditing.style.padding = '5px';
        divEditing.style.marginBottom = '10px';
        divEditing.onclick = () => {
            this.imageAnnoApp.app.pushAction('markup', 'set-drawing-mode', ACTIVECOMMAND_MODE.RECTANGLE);
        };
        this.divTop.appendChild(divEditing);
    }
}