import { ColorBlending } from '../Enum/EnumColor';

export type CSSFilterParameter = [string, number];

export type RGBAValue = [number, number, number, number];

export type RGBAGradientStop = [
  /** RGBA tuple */
  RGBAValue,
  /** <length-percentage> decimal (01.0) for gradient position */
  number?
];
export type TintParams = {
  // tuple representing rgba background
  bg0: RGBAValue;
  // radial gradient params
  bg1?: RGBAValue;
  in?: number;
  out?: number;
  mode?: ColorBlending | string;
};

export interface FilterData {
  filters: CSSFilterParameter[];
  tints: TintParams[];
}

export type FilterName = string;

export type FilterPresets = Record<FilterName, FilterData>;

