import { ProcessorType, ExpressImageAppType } from '../../../../lib';
import { ImageTools } from '..';
export class EditingTool {
    public tools: ImageTools;
    public app: any;
    constructor(tools: ImageTools) {
        this.tools = tools;
    }
    public async append(expressImageAppType = ExpressImageAppType.Editing) {
        return import(`../../../../../AppImageEditing`).then(({ AppImageEditing }) => {
            this.tools.divUpdatePre();

            // this.tools.imageWrapper.implementation.imageWrapper.view.resetImageSize();
            // console.log(this.tools.imageWrapper.implementation.app.modelController.theme.themeData);
            const params: any = {
                id: this.tools.imageWrapper.implementation.app.modelController.divHost.id,
                UI: this.tools.imageWrapper.implementation.app.param.ui,
                theme: this.tools.imageWrapper.implementation.app.param.ui ? this.tools.imageWrapper.implementation.app.modelController.theme.themeData : undefined,
                environment: this.tools.imageWrapper.implementation.app.param.environment,
            };

            this.app = new AppImageEditing(params);
            if (this.tools.imageWrapper.implementation.app.param.colorCover) {
                this.app.coverColor = this.tools.imageWrapper.implementation.app.param.colorCover;
            }
            this.app.api.setTypeImageCompression = this.tools.imageWrapper.implementation.app.typeImageCompression;
            this.app.emitAction = (data: any) => {
                this.from(data);
            }
            if (expressImageAppType === ExpressImageAppType.Effecting) {
                this.app.api.activateEffectEnv('markup')
            }
            // this.app.pushDrawingData(this.tools.imageWrapper.dataSet.paramDrawing.getDrawingData());

            // console.log(this.tools.imageWrapper.dataSet.paramDrawing.getDrawingData())

            // const drawingCanvas = this.tools.imageWrapper.dataSet.getDrawingCanvas();
            // console.log(drawingCanvas);
            // this.app.pushCanvasDrawing(drawingCanvas);

            // const exv = new ExpressImageViewer(this.app.core.canvas.canvas, param.width, param.height)
            // console.log(exv);
            // exv.updateGeometry(
            //     this.tools.imageWrapper.dataSet.paramDrawing.data
            // )
            const geo = this.tools.imageWrapper.implementation.imageWrapper.dataSet.paramDrawing.toJSON();
            // console.log(geo)
            if (geo.geometry) {
                // console.log('geo from data model to editing env' , geo)
                this.app.pushDrawingData(geo.geometry);
                this.app.setScaleGlobal(geo.scale)

            }

            this.app.initImageByImg(this.tools.imageWrapper.implementation.imageWrapper.view.imgOrigin);

            this.app.api.initEffect(this.tools.imageWrapper.implementation.imageWrapper.dataSet.paramEffecting.toJSON());
            // if (this.tools.imageWrapper.implementation.imageWrapper.dataSet.paramCropping.isModified) {
            const param = this.tools.imageWrapper.implementation.imageWrapper.getParamCrop();
            // console.log('param init', param);
            this.app.api.initFrame(param.x, param.y, param.width, param.height, param.scale, param.rotation, param.shape, param.aspectRatio);
            // }
        }).catch((err) => {
            console.debug('Stop loading Image Editing App.');
            console.debug(err);
            throw err;
        });
    }
    public from(dataIn: any) {
        const index = dataIn.indexOf(this.tools.delimiter);
        const action = index < 0 ? [dataIn] : [dataIn.substring(0, index), dataIn.substring(index + 1, dataIn.length)];
        let data;

        // console.log('dataIn', dataIn);
        // console.trace();

        switch (action[0]) {
            case 'destroy':
                this.tools.appDestroy();
                break;
            case 'update-container-size':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'post-update':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction('post-update@');
                break;
            case 'ready':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                break;
            case 'filter':
                data = JSON.parse(action[1]);
                this.tools.imageWrapper.dataSet.paramEffecting.update(data);
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(`filter@${data.typePreset}`);
                break;
            case 'history':
                this.tools.imageWrapper.implementation.app.modelController.io.emitAction(dataIn);
                this.tools.imageWrapper.dataSet.paramDrawing.updateDrawingData(this.app.api.getDrawing());
                break
            case 'update':
                data = JSON.parse(action[1]);
                // console.log('update',data);
                // console.trace();
                this.update(data);
                break;
            case 'update-geometry':
                this.tools.imageWrapper.dataSet.updateDrawingData(JSON.parse(action[1]));
                break
            default:
                break;
        }

    }
    private update(data: any) {
        this.tools.imageWrapper.dataSet.updateEffecting(data.filter);
        const cropData = data.crop
        switch (this.tools.imageWrapper.implementation.app.modelController.processorType) {
            case ProcessorType.Css:
                this.tools.imageWrapper.implementation.imageWrapper.updateImgCropByCSS(cropData.x, cropData.y, cropData.width, cropData.height, cropData.scale, cropData.rotation, cropData.shape, cropData.aspectRatio);
                break;
            // default:
            //     this.tools.imageWrapper.implementation.imageWrapper.updateImgCrop(cropData.x, cropData.y, cropData.width, cropData.height, cropData.scale, cropData.rotation,  cropData.shape, cropData.aspectRatio);
            //     break;
            default:
                const img = this.tools.editingTool.app.getImage();
                this.tools.imageWrapper.implementation.imageWrapper.updateImgCropByImg(img, cropData.x, cropData.y, cropData.width, cropData.height, cropData.scale, cropData.rotation, cropData.shape, cropData.aspectRatio);
                break;
        }

        this.tools.exImageViewer.updateCanvasSize(200, 200);
        this.tools.imageWrapper.updateDrawing();
    }
}
