import { ExpressImageAppType, FrameShapeType, FrameRatioPreset } from '../lib';
import { ExpressImageApp } from '..';
export class API {
    public app: ExpressImageApp;
    constructor(app: ExpressImageApp) {
        this.app = app;
    }
    public undo(app: string) {
        if (app === ExpressImageAppType.Editing || app === ExpressImageAppType.Effecting) {
            this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickUndo();
        } else if (app === ExpressImageAppType.Drawing) {
            this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickUndo();
        }
    }
    public redo(app: string) {
        if (app === ExpressImageAppType.Editing || app === ExpressImageAppType.Effecting) {
            this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickRedo();
        } else if (app === ExpressImageAppType.Drawing) {
            this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickRedo();
        }
    }
    public rotateCropping(degree: number) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.onRotateCropping(degree);
    }
    public zoomCropping(level: number) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.onZoomLevel(level);
    }
    public setScaleGraphics(v: number, absolute: boolean = true, undoNeeded: boolean = true) {
        this.app.implementation.imageWrapper.tools.drawingTool.app.api.setScaleGraphics(v, absolute, undoNeeded);
    }
    public scaleUpGraphics() {
        this.app.implementation.imageWrapper.tools.drawingTool.app.api.setScaleGraphics(0.1, false);
    }
    public scaleDownGraphics() {
        this.app.implementation.imageWrapper.tools.drawingTool.app.api.setScaleGraphics(-0.1, false);
    }
    public activateEffectEnv(fromApp: string = ExpressImageAppType.Editing) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.activateEffectEnv(fromApp);
    }
    public deactivateEffectEnv() {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.deactivateEffectEnv();
    }
    public setColor(appType: ExpressImageAppType, color: string, undoNeeded: boolean = true) {
        switch (appType) {
            case ExpressImageAppType.Drawing:
                this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickSetColor(color, undoNeeded);
                break;
            default:
                break;
        }
    }
    public setDrawingMode(action: string) {
        if (this.app.implementation.imageWrapper.tools.drawingTool.app) {
            this.app.implementation.imageWrapper.tools.drawingTool.app.pushAction(action);
        }
    }
    public revertToOriginal(appType: ExpressImageAppType) {
        // console.log(appType)
        // this.app.implementation.imageWrapper.dataSet.clearData();
        switch (appType) {
            case ExpressImageAppType.Drawing:
                this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickRevertToOriginal();
                break;
            default:
                this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickRevertToOriginal();
                break;
        }
        // switch (appType) {
        //     case ExpressImageAppType.Drawing:
        //         break;
        //     default:
        //         // this.app.implementation.imageWrapper.dataSet.clearData();
        // //         this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickRevertToOriginal();
        // //         this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickRevertToOriginal()
        // //         // this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickRevertToOriginal(
        // //         //     this.app.implementation.imageWrapper.view.imgOrigin, this.app.implementation.imageWrapper.dataSet.paramCropping.toString()
        // //         // );
        // //         // this.app.implementation.imageWrapper.tools.drawingTool.updateImg(this.app.implementation.imageWrapper.view.imgOrigin, this.app.implementation.imageWrapper.dataSet.paramCropping.toString());
        //         break;
        // }
    }
    public shiftShape(type: FrameShapeType) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickShiftShape(type);
    }
    public shiftShapeRatio(type: FrameRatioPreset) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickShiftShapeRatio(type);
    }
    public shiftFilterPreset(filterName: string) {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.shiftFilterPreset(filterName);
    }
    public reset() {
        // TODO:
    }
    public resizeByWindow() {
        this.app.implementation.imageWrapper.resizeByWindow();
    }
    public destroy() {
        this.app.implementation.imageWrapper.destroy();
        this.app.modelController.destroy()
        try {
            this.app.modelController.divHost.removeChild(this.app.modelController.div);
        } catch (error) {
            // TODO:
        }

    }
    public destroyEditing() {
        this.app.implementation.imageWrapper.tools.editingTool.app.api.onClickPlace();
        this.app.implementation.imageWrapper.tools.editingTool.app.destroy();
    }
    public destroyDrawing() {
        this.app.implementation.imageWrapper.dataSet.paramDrawing.updateDrawingData(
            this.app.implementation.imageWrapper.tools.drawingTool.app.api.getDataGeometry()
            );
        this.app.implementation.imageWrapper.dataSet.paramDrawing.scale = this.app.implementation.imageWrapper.tools.drawingTool.app.api.getScaleFactor()


        this.app.implementation.imageWrapper.tools.drawingTool.app.api.onClickPlace();
        this.app.implementation.imageWrapper.tools.drawingTool.app.destroy();
    }
    public getData(computeImgCtx = false, appClosingBySave = false) {
        // this.onClickDownload(); // for debugging
        return this.app.implementation.imageWrapper.getData(computeImgCtx, appClosingBySave);
    }
    public resizeDiv(v: number) {
        if (v < 0.3) {
            this.app.implementation.imageWrapper.updateSizeDiv(25);
        } else if (v > 0.6) {
            this.app.implementation.imageWrapper.updateSizeDiv(90);
        } else {
            this.app.implementation.imageWrapper.updateSizeDiv(50);
        }
    }
    public onClickImportImgByURL(url: string, needEditingApp: boolean = false) {
        this.app.implementation.imageWrapper.importImgByURL(url, needEditingApp);
    }
    public onClickAppendApp(appType: string) {
        this.app.implementation.imageWrapper.appendApp(appType);
    }
    public onClickDownload() {
        this.app.implementation.commonProcess.downloadImage();
    }
    public onToggleInteractionMode() {
        this.app.implementation.commonProcess.toggleInteractionMode();
    }
}
