import { ExpressImageAppType, FrameShapeType, FrameRatioPreset, ActionType,  } from '../../lib';
import { StateProcessing } from './StateProcessing';
import { ModelController } from '..';
export class IO {
    public controller: ModelController;
    public stateProcessing: StateProcessing;
    private init = true;
    constructor(controller: ModelController) {
        this.controller = controller;
        this.stateProcessing = new StateProcessing(this);

    }
    public emitAction(data: any) {
        // console.log(data);
        // console.trace();

        // Once the app fully updated, we hijack the history stack in order to post-update of app.
        if (this.init && data.split('@')[0] === 'post-update') {
            this.init = false;
            this.controller.app.postUpdate();

            if (this.controller.app.param.appDefault === ExpressImageAppType.Editing) {
                this.controller.app.emitAction('update-container-size@original');
            } else {
                this.controller.app.emitAction('update-container-size@');
            }
        }

        this.controller.app.emitAction(data);
    }
    public pushAction(app: any, command: any, parameter: any) {
        // console.log('=========================');
        // console.log('pushAction', app, command, parameter );

        this.switchEnvironment(app, parameter);

        if (app === 'markup' && command === 'set-drawing-mode') {
            this.stateProcessing.pushAction(ActionType.SetDrawingMode + '@' + parameter);
        }

        if (app === 'markup' && command) {
            this.pushActionMarkup(command, parameter);
        }

        if ((app === 'crop' || app === 'filter') && command) {
            this.pushActionCropFilter(command, parameter);
        }
    }
    private switchEnvironment(app: any, parameter: any) {
        if ((app === 'crop' && parameter === 'markup') || (app === 'filter' && parameter === 'markup') ) {
            // from Editing to Drawing

            // check for revert original to remove all geometries
            const len = this.controller.app.implementation.imageWrapper.tools.editingTool.app.api.getGeometryLength();
            if (len === 0) {
                this.controller.app.implementation.imageWrapper.tools.drawingTool.revertToOriginal = true;
            }

            this.stateProcessing.pushAction('destroyApp@' + ExpressImageAppType.Editing);
            this.stateProcessing.pushAction('activateApp@' + ExpressImageAppType.Drawing);
            // this.stateProcessing.io.controller.app.api.deactivateEffectEnv();
        }

        if (app === 'markup' && parameter === 'crop') {
            // from Drawing to Editing
            this.stateProcessing.pushAction('destroyApp@' + ExpressImageAppType.Drawing);
            this.stateProcessing.pushAction('activateApp@' + ExpressImageAppType.Editing);
        } else if (app === 'filter' && parameter === 'crop') {
            this.stateProcessing.io.controller.app.api.deactivateEffectEnv();
        }

        if (parameter === 'filter') {
            if (app === 'crop') {
                this.stateProcessing.io.controller.app.api.activateEffectEnv();
            } else if (app === 'markup') {
                this.stateProcessing.pushAction('destroyApp@' + ExpressImageAppType.Drawing);
                this.stateProcessing.pushAction('activateApp@' + ExpressImageAppType.Effecting);
                // this.stateProcessing.io.controller.app.api.activateEffectEnv();
            }

        }
    }
    // This pushAction fn is the same pipeline for both apps : filter and crop.
    private pushActionCropFilter(command: any, parameter: any) {
        switch (command) {
            case 'undo':
                this.controller.app.api.undo(ExpressImageAppType.Editing);
                break;
            case 'redo':
                this.controller.app.api.redo(ExpressImageAppType.Editing);
                break;
            case 'shape':
                this.controller.app.api.shiftShape(parameter as FrameShapeType);
                break;
            case 'aspect-ratio':
                this.controller.app.api.shiftShapeRatio(parameter as FrameRatioPreset);
                break;
            case 'revert-to-original':
                this.controller.app.api.revertToOriginal(ExpressImageAppType.Editing);
                break;
            case 'set-filter-preset':
                this.controller.app.api.shiftFilterPreset(parameter);
                break;
            default:
                break;
        }
    }
    private pushActionMarkup(command: any, parameter: any) {
        switch (command) {
            case 'init-scale':
                this.controller.app.api.setScaleGraphics(+parameter, true, false);
                break;
            case 'set-scale':
                this.controller.app.api.setScaleGraphics(+parameter);
                break;
            case 'offset-scale':
                this.controller.app.api.setScaleGraphics(+parameter, false);
                break;
            case 'undo':
                this.controller.app.api.undo(ExpressImageAppType.Drawing);
                break;
            case 'redo':
                this.controller.app.api.redo(ExpressImageAppType.Drawing);
                break;
            case 'init-color':
                const undoNeeded = false;
                this.controller.app.api.setColor(ExpressImageAppType.Drawing, parameter, undoNeeded);
                break;
            case 'set-color':
                this.controller.app.api.setColor(ExpressImageAppType.Drawing, parameter);
                break;
            case 'revert-to-original':
                this.controller.app.api.revertToOriginal(ExpressImageAppType.Drawing);
                break;
            default:
                break;
        }
    }
}
