export const renderPolyline = (ctx: CanvasRenderingContext2D, pos: Position[], color: number[], offset: number[] =  [0, 0], scale: number = 1) => {
    ctx.strokeStyle = `rgba(${color[0]}, ${color[1]}, ${color[2]}, ${color[3]})`;
    ctx.lineWidth = scale;
    ctx.beginPath();
    ctx.moveTo(pos[0].sx - offset[0], pos[0].sy - offset[1]);
    for (let i = 1 ; i < pos.length; ++i) {
        ctx.lineTo(pos[i].sx - offset[0], pos[i].sy - offset[1]);
    }
    ctx.lineCap = 'round';
    ctx.stroke();
    ctx.lineCap = 'butt';
};
export const renderPolygon = (ctx: CanvasRenderingContext2D, pos: Position[], color: number[], offset: number[] =  [0, 0], scale: number = 1) => {
    // console.log('renderPolygon', color);
    ctx.fillStyle = 'rgba(8, 127, 155, 0.3)';
    ctx.strokeStyle = `rgba(${color[0]}, ${color[1]}, ${color[2]}, ${color[3]})`;
    ctx.lineWidth = scale;
    ctx.beginPath();
    ctx.moveTo(pos[0].sx - offset[0], pos[0].sy - offset[1]);
    for (let i = 1 ; i < pos.length; ++i) {
        ctx.lineTo(pos[i].sx - offset[0], pos[i].sy - offset[1]);
    }
    ctx.closePath();
    // ctx.fill();
    ctx.stroke();
};
export const renderCircle = (ctx: CanvasRenderingContext2D, pos: Position[], color: number[], offset: number[] =  [0, 0], scale: number = 1) => {
    const col = `rgba(${color[0]}, ${color[1]}, ${color[2]}, ${color[3]})`;
    // ctx.fillStyle = 'rgba(8, 127, 155, 0.3)';
    ctx.strokeStyle = col;
    ctx.lineWidth = scale;
    const rX = (pos[2].sx - pos[0].sx) * 0.5 ;
    const rY = (pos[2].sy - pos[0].sy) * 0.5 ;
    const x = pos[0].sx + rX - offset[0];
    const y = pos[0].sy + rY - offset[1];
    ctx.beginPath();
    ctx.ellipse(x,y, rX, rY, 0, 0, 2 * Math.PI);
    ctx.closePath();
    // ctx.fill();
    ctx.stroke();
};
export const renderArrowSingle = (ctx: CanvasRenderingContext2D, pos: Position[], color: number[], offset: number[] =  [0, 0], scale: number = 1) => {
    const col = `rgba(${color[0]}, ${color[1]}, ${color[2]}, ${color[3]})`;
    ctx.strokeStyle = col;
    ctx.fillStyle = col;
    ctx.lineWidth = scale;
    arrow(ctx, pos, offset, scale, false);
};
export const renderArrowDouble = (ctx: CanvasRenderingContext2D, pos: Position[], color: number[], offset: number[] =  [0, 0], scale: number = 1) => {
    const col = `rgba(${color[0]}, ${color[1]}, ${color[2]}, ${color[3]})`;
    ctx.strokeStyle = col;
    ctx.fillStyle = col;
    ctx.lineWidth = scale;
    arrow(ctx, pos, offset, scale, true);
};
const arrow = (ctx: CanvasRenderingContext2D, pos: Position[], offset: number[] =  [0, 0], scale: number = 1, single: boolean = false ) => {
    const arrayVertices = [pos[0].sx - offset[0], pos[0].sy - offset[1], pos[1].sx - offset[0], pos[1].sy - offset[1], pos[2].sx - offset[0], pos[2].sy - offset[1]]
    const weight: number[] = [];
    for (let i = 0; i < pos.length; ++i) {
      weight.push(0.5);
    }
    const pts: number[] = [];
    for (const ii of arrayVertices) {
      pts.push(ii);
    }
    let arrayControlPoint: number[] = [];
    for (let i = 0; i < pts.length - 4; i += 2) {
      arrayControlPoint = arrayControlPoint.concat(
          getControlPoints( pts[i], pts[i + 1], pts[i + 2], pts[i + 3], pts[i + 4], pts[i + 5], weight[i])
      );
    }
    drawSpline(ctx, arrayVertices, arrayControlPoint, scale, single);
};
const getControlPoints = ( x0: number, y0: number, x1: number, y1: number, x2: number, y2: number, t: number) => {
    const d01 = Math.sqrt(Math.pow(x1 - x0, 2) + Math.pow(y1 - y0, 2));
    const d12 = Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
    const fa = (t * d01) / (d01 + d12);
    const fb = t - fa;
    const p1x = x1 + fa * (x0 - x2);
    const p1y = y1 + fa * (y0 - y2);
    const p2x = x1 - fb * (x0 - x2);
    const p2y = y1 - fb * (y0 - y2);
    return [p1x, p1y, p2x, p2y];
};
const drawSpline = (ctx: CanvasRenderingContext2D, APoints: number[], cps: number[], scale: number = 1, single: boolean = true) => {
    const pts = APoints;
    const cp = cps;
    const n = pts.length;
    // const lineDashOffset = 3;
    curveQuadraticArrow(ctx, pts[0], pts[1], cp[0], cp[1], pts[2], pts[3]);
    curveQuadraticArrow(ctx, pts[n - 2], pts[n - 1], cp[2 * n - 10], cp[2 * n - 9], pts[n - 4], pts[n - 3]);
    cArrowByTranslation(ctx, new NVector3(pts[4], pts[5]), new NVector3(cp[2], cp[3]), pts[4], pts[5], scale);
    if (single) {
        cArrowByTranslation(ctx, new NVector3(pts[0], pts[1]), new NVector3(cp[0], cp[1]), pts[0], pts[1], scale);
    }
};
const curveQuadraticArrow = (ctx: CanvasRenderingContext2D, x0: number, y0: number, x1: number, y1: number, x2: number, y2: number) => {
    ctx.beginPath();
    ctx.moveTo(x2, y2);
    ctx.quadraticCurveTo(x1, y1, x0, y0);
    ctx.stroke();
    ctx.setLineDash([]);
};
const cArrowByTranslation = (ctx: CanvasRenderingContext2D, vec0: NVector3, vec1: NVector3, x0: number, y0: number, arrowScale: number = 1) => {
    const degree = getRotationByWorldCoordinateByTwo(vec0, vec1);
    ctx.save();
    ctx.translate(x0, y0);
    ctx.rotate(-((degree * Math.PI) / 180));
    const height = 7.5 * 0.5; // this.shape.draw.renderer.imageAnnoApp.customization.themeCurrent.arrow.scale * scaleRender; // 7.5;
    const depth = -12 * 0.5; // this.shape.draw.renderer.imageAnnoApp.customization.themeCurrent.arrow.scale * scaleRender; // 12;
    ctx.beginPath();
    ctx.moveTo(0, 0);
    ctx.lineTo(-(depth * arrowScale), -height * arrowScale);
    ctx.lineTo(-(depth * arrowScale), height * arrowScale);
    ctx.closePath();
    ctx.stroke();
    ctx.fill();
    ctx.restore();
};
const getRotationByWorldCoordinateByTwo = (v0: NVector3, v1: NVector3): number => {
    const v = v1.sub(v0);
    const origin = new NVector3(1, 0, 0);
    if (v.y > 0) {
        return getAngleByTwoVectors(origin, v) * -1;
    }
    return getAngleByTwoVectors(origin, v);
};
const getAngleByTwoVectors = (v0: NVector3, v1: NVector3): number => {
    return (getAngleByTwoVectorsRadian(v0, v1) * 180.0) / Math.PI;
};
const getAngleByTwoVectorsRadian = (v0: NVector3, v1: NVector3): number => {
    return Math.acos(getCosAngleByTwoVectors(v0, v1));
};
const getCosAngleByTwoVectors = (v0: NVector3, v1: NVector3): number => {
    return getDotProduct(normalize(v0), normalize(v1));
};
const getDotProduct = (v1: NVector3, v2: NVector3): number => {
    return v1.x * v2.x + v1.y * v2.y + v1.z * v2.z;
};
const normalize = (v: NVector3): NVector3 => {
    const len: number = Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
    if (len < 0.0001) {
      return new NVector3(v.x, v.y, v.z);
    }
    return new NVector3(v.x / len, v.y / len, v.z / len);
};
