import { Implementation } from '..';
import { importImage, ProcessorType, ExpressImageAppType, InteractionMode, FrameShapeType, FrameRatioPreset, Environment } from '../../lib'
import { View } from './View';
import { ExpressImageData } from './ExpressImageData';
import { ImageTools } from './Tools';

export class ImageWrapper {
    public implementation: Implementation;
    public view: View;
    public dataSet: ExpressImageData;
    public tools: ImageTools;
    constructor(implementation: Implementation) {
        this.implementation = implementation;
        this.tools = new ImageTools(this);
        this.view = new View(this);
        this.dataSet = new ExpressImageData(this);
    }
    public async importImgByURL(url: string = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/bg/zoom-James-River-Kayaking.jpg', needEditingApp: boolean = false) {
        this.view.clear();
        const imgOrigin = await importImage(url) as HTMLImageElement;
        this.view.imgOrigin = imgOrigin;
        this.view.img = imgOrigin;

        switch (this.implementation.app.modelController.processorType) {
            case ProcessorType.Css:
                this.view.initDivForCSS(this.view.imgOrigin);
                break;
            default:
                // TEMP HACK: this will allow re-editing image FIXME:
                try {
                    this.view.initDivForImgModified(this.view.imgOrigin);
                } catch(e) {
                    console.log('importImgByURL->initDivForImgModified', e)
                    return
                }
                this.tools.appendApp(ExpressImageAppType.Viewer);
                break;
        }
        this.dataSet.resetData();
        if (needEditingApp) {
            this.tools.editingTool.append();
        }
        if (this.implementation.app.param.data) {
            // console.debug(' data binding needed', this.implementation.app.param.data);
            // console.log(this.implementation.app.param.data);

            if (this.implementation.app.param.data.editing) {
                const data = this.implementation.app.param.data.editing;
                this.dataSet.updateParamCrop(data.x, data.y, data.width, data.height, data.scale, data.rotation, data.shape as FrameShapeType, data.aspectRatio);
            }

            // if (this.implementation.app.param.data.drawing) {
            //     const data = this.implementation.app.param.data.drawing;
            //     this.dataSet.updateDrawingData(data);

            // }

        }
    }
    public getData(computeImgCtx = false, appClosingBySave = false) {
        const dataOut = this.tools.imageWrapper.dataSet.updateData(computeImgCtx, appClosingBySave);
        // console.debug('dataOut', dataOut);
        return dataOut;
    }
    public getParamCrop() {
        return this.dataSet.paramCropping;
    }
    public updateImgCropByImg(img: HTMLImageElement, x: number, y:number, width: number, height: number, scale: number, rotation: number, shapeType: FrameShapeType, aspectRatio: FrameRatioPreset) {
        this.dataSet.updateParamCrop(x, y, width, height, scale, rotation, shapeType, aspectRatio);
        this.view.updateImage(img);

    }
    public updateImgCropByCSS(x: number, y:number, width: number, height: number, scale: number, rotation: number, shapeType: FrameShapeType, aspectRatio: FrameRatioPreset) {
        this.dataSet.updateParamCrop(x, y, width, height, scale, rotation, shapeType, aspectRatio);

        this.view.img.style.objectPosition = `${-this.dataSet.paramCropping.x}px ${-this.dataSet.paramCropping.y}px `;
        const transform =  `scale(${scale}) rotate(${(rotation * 180 / Math.PI)}deg)`;
        this.view.img.style.transform = transform;

        this.view.img.width = this.dataSet.paramCropping.width;
        this.view.img.height = this.dataSet.paramCropping.height;
        // console.log('this.view.img.style');
        // console.log(this.view.img.style);
        // console.log(this.view.img, scale, rotation, transform);
    }
    public updateImgCrop(x: number, y:number, width: number, height: number, scale: number, rotation: number, shapeType: FrameShapeType, aspectRatio: FrameRatioPreset) {
        this.dataSet.updateParamCrop(x, y, width, height, scale, rotation, shapeType, aspectRatio);
    }
    public updateEnvironment(v: Environment) {
        this.tools.updateEnvironment(v);
    }
    public toJSON() {
        return JSON.stringify(this.dataSet.paramCropping);
    }
    public resizeByWindow() {
        this.updateDrawing();
    }
    public updateSizeDiv(v: number) {
        this.view.updateSizeDiv(v);
        // console.log(v);
        if(v < 30) {
            this.tools.exImageViewer.renderScale = 0.5;
        } else if (v > 80) {
            this.tools.exImageViewer.renderScale = 1.2;
        } else {
            this.tools.exImageViewer.renderScale = 1.0;
        }

        this.updateDrawing();
    }
    public updateDrawing() {
        this.tools.exImageViewer.updateDrawing();
    }
    public appendApp(appType: string) {
        this.tools.appendApp(appType);
    }
    public updateViewMode(mode: InteractionMode = InteractionMode.Drawing) {
        this.tools.exImageViewer.app.updateMode(mode);
    }
    public destroy() {
        this.tools.drawingTool.app.destroy();
        this.tools.editingTool.app.destroy();
        // this.tools.effectingTool.app?.destroy();
    }
}
