import { FrameShapeType } from '../Enum/EnumExpressImage'
import { getFiltersString } from '../Color/ImageFilterPreset';
export const trimContextByShape = (ctx: CanvasRenderingContext2D, frameShapeType: FrameShapeType, x: number, y: number, width: number, height: number, offset = true, invert = false) => {
    if (!offset) {
        x = 0;
        y = 0;
    }
    ctx.beginPath();
    if (!invert) {
        ctx.rect(-1, -1, ctx.canvas.width + 2, ctx.canvas.height + 2);
    }
    switch (frameShapeType) {
        case FrameShapeType.RectangleRounded:
            const angle = 3.14 * 0.5;

            // const r = 100; // const value
            let r = (width + height) * 0.5 * 0.05; // dynamic value by the size of image
            r = r > width * 0.5 ? width * 0.5 - 10 : r;
            r = r > height * 0.5 ? height * 0.5 - 10 : r;

            ctx.moveTo(x, y);
            ctx.arc(x + width - r, y + r, r, angle * 3 , angle * 4 , false);
            ctx.arc(x + width - r, y + height - r, r, angle * 0 , angle * 1 , false);
            ctx.arc(x + r, y + height - r, r, angle * 1 , angle * 2 , false);
            ctx.arc(x + r, y + r, r, angle * 2 , angle * 3 , false);
            break;
        case FrameShapeType.Circle:
            const widthHalf = width * 0.5;
            const heightHalf = height * 0.5;
            ctx.ellipse(x +  widthHalf, y + heightHalf, widthHalf, heightHalf, 0, 0, Math.PI * 2);
            break;
        default:
            ctx.rect(x, y, width, height);
            break;
        }
    ctx.closePath();
    ctx.fill('evenodd');
};
export const cutOutContextByShape = (ctx: CanvasRenderingContext2D, frameShapeType: FrameShapeType, x: number, y: number, width: number, height: number, offset = true, invert = false) => {
    ctx.globalCompositeOperation = 'destination-out';
    ctx.fillStyle = `rgba(255, 255, 255, 1)`;
    trimContextByShape(ctx, frameShapeType, x, y, width, height, offset, invert);
    ctx.globalCompositeOperation = 'source-over';
    return ctx;
};
export const maskOutContextByShape = (ctx: CanvasRenderingContext2D, frameShapeType: FrameShapeType, x: number, y: number, width: number, height: number, offset = true) => {
    return cutOutContextByShape(ctx, frameShapeType, x, y, width, height, offset, true);
};
export const canvasMergeImg = (img: HTMLImageElement, layerCanvas: HTMLCanvasElement, width: number, height: number) => {
    const canvasOut = document.createElement('canvas');
    // canvasOut.width = img.width;
    // canvasOut.height = img.height;
    canvasOut.width = width;
    canvasOut.height = height;

    const ctxOut = canvasOut.getContext('2d') as CanvasRenderingContext2D;
    ctxOut.drawImage(img, 0, 0, width, height);
    ctxOut.drawImage(layerCanvas, 0, 0, width, height);

    const imgOut = new Image();
    imgOut.src = canvasOut.toDataURL();
    return imgOut;
};
export const downloadImage = (img: HTMLImageElement, imgTitle: string = 'myImage') => {
    const link = document.createElement('a');
    link.download = `${imgTitle}.png`;
    link.href = img.src;
    link.click();
};
export const canvasFilter = (ctx: CanvasRenderingContext2D, data: any, x: number, y: number) => {
    const canvas = ctx.canvas;
    // console.log(canvas, data)
    const canvasOut = document.createElement('canvas');
    canvasOut.width = canvas.width;
    canvasOut.height = canvas.height;
    const ctxOut = canvasOut.getContext('2d') as CanvasRenderingContext2D;
    const filter = getFiltersString(data.filters, data.inter)
    ctxOut.filter = filter;
    ctxOut.drawImage(canvas, 0, 0, canvas.width, canvas.height);
    // ctxOut.filter = 'none';
    return ctxOut;
};
