export const deg2rad = Math.PI / 180;
export const rad2deg = 180 / Math.PI;
export const distance = (x0: number, y0: number, x1: number, y1: number) => {
  return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));
};
export const series = (s: number = 0, e: number = 10, step: number = 1) => {
  let series: number[] = [];
  for (let i = 0; i < e; i += step) { series.push(i); }
  return series;
};
export const range = (s: number = 0, e: number = 1, rNumber: number = 10) => {
  let range: number[] = [];
  let theStep = e / rNumber;
  for (let i = 0; i < e; i += theStep) { range.push(i); }
  return range;
};
