// region get ReMap Nomalize From 0.0 to 1.0
export const remapNomalize = (CValue: number[]) => {
  let valueOut: number[] = [];
  let domain: number[] = getDomain(CValue);
  for (let d of CValue) { valueOut.push(((d - domain[0]) / (domain[1] - domain[0]))); }
  return valueOut;
};
// endregion get ReMap Nomalize From 0.0 to 1.0

// region get re-map
export const remaps = (CValue: number[], OldMin: number, OldMax: number, NewMin: number, NewMax: number) => {
  let temp: number[] = [];
  for (let d of CValue) { temp.push(remap(d, OldMin, OldMax, NewMin, NewMax)); }
  return temp;
};
export const remap = (CValue: number, OldMin: number, OldMax: number, NewMin: number, NewMax: number) => {
  return (((CValue - OldMin) * (NewMax - NewMin)) / (OldMax - OldMin)) + NewMin;
};
export const getDomain = (doubleList: number[]) => {
  let Min: number = Number.MAX_VALUE; let Max: number = Number.MIN_VALUE;
  for (let d of doubleList) { if (Min > d) { Min = d; } if (Max < d) { Max = d; } }
  return [Min, Max];
};
export const getIndexBySort = (d: number[]) => {
  let len = d.length;
  let indices = new Array(len);
  for (let i = 0; i < len; ++i) { indices[i] = i; }
  indices.sort((a, b) => { return d[a] < d[b] ? -1 : d[a] > d[b] ? 1 : 0; });
  return indices;
};
export const getMaxIndex = (d: number[]) => {
  let index = -1;
  let max = Number.MIN_SAFE_INTEGER;
  for (let i = 0; i < d.length; ++i) {
      if (max < d[i]) { max = d[i]; index = i; }
  }
  return index;
};
