export const getRatioByWidthHidthByWidths = (originWidth: number, originHeight: number, newWidth: number) => {
  return [newWidth, originHeight * (newWidth / originWidth)];
};
export const getRatioByWidthHidthByHeight = (originWidth: number, originHeight: number, newHeight: number) => {
  return [originWidth * (newHeight / originHeight), newHeight];
};
export const getRatioByNewWidth = (originWidth: number, originHeight: number, newWidth: number) => {
  return [newWidth, originHeight * (newWidth / originWidth)];
};
export const getRatioByNewHeight = (originWidth: number, originHeight: number, newHeight: number) => {
  return [originWidth * (newHeight / originHeight), newHeight];
};
export const isContainNagativeNumber = (vs: number[]) => {
  for (let i = 0; i < vs.length; ++i) { if (vs[i] < 0.0) { return true; } }
  return false;
};
