// #region remap geo utility
export const remapMeterFromGPSPos = (lat1: number, lon1: number, lat2: number, lon2: number) => {  // generally used geo measurement function
  let R = 6378.137; // Radius of earth in KM
  let dLat = lat2 * Math.PI / 180 - lat1 * Math.PI / 180;
  let dLon = lon2 * Math.PI / 180 - lon1 * Math.PI / 180;
  let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) *
      Math.sin(dLon / 2) * Math.sin(dLon / 2);
  let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  let d = R * c;
  return d * 1000; // meters
};
export const getKilogramFromNewton = (w: number): number => { return w * 0.10197162129779283; };
export const getNewtonFromKilogram = (w: number): number => { return w * 9.80665; };
export const remapMileFromMeter = (meter: number): number => { return meter * 0.000621371; };
export const remapMeterFromMile = (meter: number): number => { return meter * 1609.34; };
export const converterUnitLength = (value: number, from: Unit, to: Unit) => {
  // ConverterUnitLengthFromMeter
  switch (from) {
      case Unit.Nanometer: return value * 1000000000;
      case Unit.Micrometer: return value * 1000000;
      case Unit.Millimeter: return value * 1000;
      case Unit.Centimeter: return value * 100;
      case Unit.Meter: return value;
      case Unit.Inch: return value * 39.37007874;
      case Unit.Foot: return value * 3.280839895;
      case Unit.Yard: return value * 1.0936132983;
      case Unit.Mile: return value * 0.0006213689;
      case Unit.Kilometer: return value * 0.001;
      default: return value;
  }
};
export const converterUnitLengthFromMeter = (value: number, unitLength: Unit) => {
  switch (unitLength) {
      case Unit.Nanometer: return value * 1000000000;
      case Unit.Micrometer: return value * 1000000;
      case Unit.Millimeter: return value * 1000;
      case Unit.Centimeter: return value * 100;
      case Unit.Meter: return value;
      case Unit.Inch: return value * 39.37007874;
      case Unit.Foot: return value * 3.280839895;
      case Unit.Yard: return value * 1.0936132983;
      case Unit.Mile: return value * 0.0006213689;
      case Unit.Kilometer: return value * 0.001;
      default: return value;
  }
};
export const milesFromMeters = (m: number) => { return m * 0.00062137; };
export const milesFromKM = (m: number) => { return m * 0.62137119; };
