export const eps: number = 0.0000001;
export class NVector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    constructor(x: number, y: number, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    public mult(v: number): NVector3 {
        this.x *= v;
        this.y *= v;
        this.z *= v;
        return new NVector3(this.x, this.y, this.z);
    }
    public divByVal(v: number): NVector3 {
        this.x /= v;
        this.y /= v;
        this.z /= v;
        return new NVector3(this.x, this.y, this.z);
    }
    public add(v: NVector3): NVector3 {
        this.x += v.x;
        this.y += v.y;
        this.z += v.z;
        return new NVector3(this.x, this.y, this.z);
    }
    public sub(v: NVector3): NVector3 {
        this.x -= v.x;
        this.y -= v.y;
        this.z -= v.z;
        return new NVector3(this.x, this.y, this.z);
    }
    public unitize(): NVector3 {
        return this.normalize();
    }
    public normalize(): NVector3 {
        let len: number = Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
        if (len < 0.0001) {
            return this;
        }
        this.x = this.x / len;
        this.y = this.y / len;
        this.z = this.z / len;
        return this;
    }
}
