import { ExpressImageAppType , Environment} from '../../../lib'
import { DrawingTool } from './Drawing';
import { EditingTool } from './Editing';
import { ExImageViewer } from './ExImageViewer';
import { ImageWrapper } from '..';
export class ImageTools {
    public imageWrapper: ImageWrapper;
    public drawingTool: DrawingTool;
    public editingTool: EditingTool;
    public exImageViewer: ExImageViewer;
    public delimiter = '@';
    public activeApp = ExpressImageAppType.Editing;
    constructor(imageWrapper: ImageWrapper) {
        this.imageWrapper = imageWrapper;
        this.drawingTool = new DrawingTool(this);
        this.editingTool = new EditingTool(this);
        this.exImageViewer = new ExImageViewer(this);
    }
    public appDestroy() {
        this.divUpdatePost();
    }
    public divUpdatePre() {
        this.imageWrapper.implementation.app.modelController.div.style.display = 'none';
    }
    public divUpdatePost() {
        if (this.imageWrapper.implementation.app.environment === Environment.Standalone) {
            this.imageWrapper.implementation.app.modelController.div.style.display = 'block';
        }
        this.imageWrapper.resizeByWindow();
    }
    public appendApp(appType: string) {
        switch (appType.toLowerCase()) {
            case ExpressImageAppType.Effecting:
                this.editingTool.append(ExpressImageAppType.Effecting);
                this.activeApp = ExpressImageAppType.Editing;
                break;
            case ExpressImageAppType.Editing:
                this.editingTool.append();
                this.activeApp = ExpressImageAppType.Editing;
                break;
            case ExpressImageAppType.Drawing:
                this.drawingTool.append();
                this.activeApp = ExpressImageAppType.Drawing;
                break;
            case ExpressImageAppType.Viewer:
                this.exImageViewer.append(this.imageWrapper.view.divImg, this.imageWrapper.view.imgOrigin.clientWidth, this.imageWrapper.view.imgOrigin.clientHeight);
                break;
            default:
                break;
        }
    }
    public updateEnvironment(v: Environment) {
        if (this.editingTool.app !== undefined) {
            this.editingTool.app.api.shiftEnvironment(v);
        }
        if (this.drawingTool.app !== undefined) {
            this.drawingTool.app.api.shiftEnvironment(v);
        }
    }
}
